import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import Login from "./pages/Login";
import Register from "./pages/Register";
import BuyerHome from "./pages/buyer/Home";
import SellerHome from "./pages/seller/Home";

import TermsAndConditions from "./components/common/website/TermsAndConditions";
import ReturnPolicy from "./components/common/website/ReturnPolicy";
import PrivacyPolicy from "./components/common/website/PrivacyPolicy";
import UserGuidelines from "./components/common/website/UserGuidelines";

import ForgotPassword from "./components/common/website/ForgotPassword";
import ResetPassword from "./components/common/website/ResetPassword";

export default function App() {
  return (
    <Router>
      <Routes>
        {/* Buyer */}
        <Route path="/" element={<BuyerHome />} />
        <Route path="/buyer/home" element={<BuyerHome />} />

        {/* Auth */}
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />

        {/* Password Reset */}
        <Route path="/forgot-password" element={<ForgotPassword />} />
        <Route path="/reset-password" element={<ResetPassword />} />

        {/* Seller */}
        <Route path="/seller/home" element={<SellerHome />} />

        {/* Website Pages */}
        <Route path="/terms-and-conditions" element={<TermsAndConditions />} />
        <Route path="/privacy-policy" element={<PrivacyPolicy />} />
        <Route path="/return-refund-policy" element={<ReturnPolicy />} />
        <Route path="/user-guidelines" element={<UserGuidelines />} />
      </Routes>
    </Router>
  );
}
