import React from "react";
import { Heart, X } from "lucide-react";
import { Product } from "../../types";

const DEFAULT_IMAGE = "https://via.placeholder.com/180";

type DisplayProductsProps = {
  products: Product[];
  hasMore: boolean;
  onLoadMore: () => void;
  onSelectProduct?: (product: Product) => void;
  onToggleWishlist?: (product: Product) => void;
  wishlist?: Product[];
  title?: string;
  onClose?: () => void;
};

const DisplayProducts: React.FC<DisplayProductsProps> = ({
  products,
  hasMore,
  onLoadMore,
  onSelectProduct,
  onToggleWishlist,
  wishlist = [],
  title = "All Products",
  onClose,
}) => {
  return (
    <div className="w-full max-w-[1440px] mx-auto px-2 md:px-4">
      {/* Header */}
      <div className="flex items-center justify-between mb-6 sticky top-0 bg-[#fafafa] z-20 py-4">
        <h2 className="text-2xl font-semibold">{title}</h2>

        {onClose && (
          <button
            onClick={onClose}
            className="flex items-center gap-1 text-gray-600 hover:text-black"
          >
            <X size={22} />
            <span className="hidden sm:inline text-sm">Close</span>
          </button>
        )}
      </div>

      {/* Grid */}
      {products.length === 0 ? (
        <p className="text-gray-500">No products found.</p>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {products.map((product) => {
            const isInWishlist = wishlist.some((w) => w.id === product.id);

            const imageSrc =
              typeof product.image === "string" && product.image
                ? product.image
                : DEFAULT_IMAGE;

            /* ---------------- Variant-based pricing ---------------- */
            const variant = product.variants?.[0];

            const basePrice =
              variant?.price ??
              variant?.est_price ??
              0;

            const price = Number(basePrice) || 0;
            const discount = Number(variant?.discount || 0);

            const finalPrice =
              discount > 0
                ? price - price * (discount / 100)
                : price;

            return (
              <div
                key={product.id}
                className="bg-white rounded-lg shadow hover:shadow-lg transition cursor-pointer border"
                onClick={() => onSelectProduct?.(product)}
              >
                {/* Image */}
                <div className="relative">
                  <img
                    src={imageSrc}
                    alt={product.name}
                    className="w-full h-48 object-cover rounded-t-lg"
                    onError={(e) =>
                      (e.currentTarget.src = DEFAULT_IMAGE)
                    }
                  />

                  {/* Wishlist */}
                  <button
                    className="absolute top-2 right-2 bg-white rounded-full p-1 shadow hover:bg-gray-200"
                    onClick={(e) => {
                      e.stopPropagation();
                      onToggleWishlist?.(product);
                    }}
                  >
                    <Heart
                      size={18}
                      fill={isInWishlist ? "red" : "white"}
                      stroke={isInWishlist ? "red" : "black"}
                    />
                  </button>
                </div>

                {/* Info */}
                <div className="p-4 space-y-1">
                  <p className="font-semibold text-sm truncate">
                    {product.name}
                  </p>

                  <p className="text-xs text-gray-600 truncate">
                    {product.variants
                      ?.map((v) => v.variant_label)
                      .join(", ") || ""}
                  </p>

                  <p className="text-xs text-gray-600">
                    <span className="font-semibold">Brand:</span>{" "}
                    {product.brand || ""}
                  </p>

                  <p className="text-xs text-gray-600">
                    <span className="font-semibold">Ref:</span>{" "}
                    {product.ref_no || ""}
                  </p>

                  {/* Price */}
                  {price > 0 ? (
                    <div className="pt-2">
                      {discount > 0 ? (
                        <div className="flex items-center gap-2">
                          <span className="text-xs text-gray-400 line-through">
                            {price.toFixed(2)}
                          </span>
                          <span className="text-sm font-semibold text-green-600">
                            {finalPrice.toFixed(2)}
                          </span>
                          <span className="text-xs bg-green-100 text-green-700 px-1.5 py-0.5 rounded">
                            {discount}% OFF
                          </span>
                        </div>
                      ) : (
                        <span className="text-sm font-semibold text-gray-900">
                          {price.toFixed(2)}
                        </span>
                      )}
                    </div>
                  ) : (
                    <p className="text-xs text-red-500 pt-2">
                      Price unavailable
                    </p>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      )}

      {/* Load More */}
      {hasMore && (
        <div className="flex justify-center mt-10">
          <button
            onClick={onLoadMore}
            className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
          >
            Load More
          </button>
        </div>
      )}
    </div>
  );
};

export default DisplayProducts;
