import React, { useEffect, useState } from "react";
import ProductsList from "../common/ProductsList";
import { Product } from "../../types";
import { getRecentlyViewed } from "../../api/api";

type RecentlyViewedProps = {
  token?: string;
  onSelectProduct: (product: Product) => void;
  onToggleWishlist: (product: Product) => void;
  wishlist: Product[];
  onSeeAll?: () => void; //  supported
};

const RecentlyViewed: React.FC<RecentlyViewedProps> = ({
  token,
  onSelectProduct,
  onToggleWishlist,
  wishlist,
  onSeeAll, //  RECEIVE
}) => {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!token) return;

    const loadRecent = async () => {
      try {
        const data = await getRecentlyViewed(token);

        // API: [{ id, product, viewed_at }]
        const extractedProducts = data.map((entry: any) => entry.product);

        setProducts(extractedProducts);
      } catch (err) {
        console.error("Failed to load recently viewed:", err);
      } finally {
        setLoading(false);
      }
    };

    loadRecent();
  }, [token]);

  if (!token) return null;
  if (loading)
    return <p className="text-gray-500 px-2">Loading recently viewed</p>;
  if (products.length === 0)
    return <p className="text-gray-500 px-2">No recently viewed items</p>;

  return (
    <ProductsList
      title="Your recently viewed items"
      products={products}
      onSelectProduct={onSelectProduct}
      onToggleWishlist={onToggleWishlist}
      wishlist={wishlist}
      onSeeAll={onSeeAll} 
    />
  );
};

export default RecentlyViewed;
