import React, { useEffect, useState, JSX } from "react";
import { useNavigate } from "react-router-dom";
import { X } from "lucide-react";

/* ---------------- COMPONENTS ---------------- */
import Header from "../../components/buyer/Header";
import BannerAd from "../../components/common/Banner";
import RecentlyViewed from "../../components/buyer/RecentlyViewed";
import ProductsList from "../../components/common/ProductsList";
import DisplayProducts from "../../components/common/DisplayProducts";
import UserProfile from "../../components/buyer/BuyerProfile";
import ProductDetails from "../../components/buyer/ProductDetails";
import CartPanel from "../../components/buyer/CartPanel";
import WishlistPanel from "../../components/buyer/WishlistPanel";
import NotificationsPanel from "../../components/common/NotificationsPanel";
import OrdersList from "../../components/buyer/OrdersList";
import PaymentPanel from "../../components/buyer/PaymentPanel";
import Chat from "../../components/common/ChatPanel";
import QuotationsPanel from "../../components/buyer/QuotationsPanel";
import About from "../../components/common/website/About";

/* ---------------- API ---------------- */
import {
  getProfile,
  getProducts,
  getCart,
  addToCart,
  clearCartItem,
  removeCartItem,
  getWishlist,
  toggleWishlist,
  searchProducts,
  getNotifications,
  markNotificationRead,
} from "../../api/api";

/* ---------------- TYPES ---------------- */
import {
  Product,
  ProductVariant,
  CartItem,
  Notification,
  WishlistItem,
} from "../../types";

import { CATEGORIES } from "../../constants/Lists";

/* ---------------- CONSTANTS ---------------- */
const DEFAULT_IMAGE = "/images/default_product.png";
const BACKEND_URL =
  process.env.NODE_ENV === "production" ? "" : "http://127.0.0.1:8000";

/* ---------------- HELPERS ---------------- */
const normalizeProducts = (products: Product[]): Product[] =>
  products.map((p) => ({
    ...p,
    image: p.image
      ? p.image.startsWith("http")
        ? p.image
        : `${BACKEND_URL}${p.image}`
      : DEFAULT_IMAGE,
    variants: p.variants || [],
  }));

const normalizeCart = (items: CartItem[]): CartItem[] =>
  items.map((item) => ({
    ...item,
    product: {
      ...item.product,
      image: item.product.image
        ? item.product.image.startsWith("http")
          ? item.product.image
          : `${BACKEND_URL}${item.product.image}`
        : DEFAULT_IMAGE,
    },
  }));

/* ====================================================== */

export default function BuyerHome() {
  const navigate = useNavigate();

  /* ---------------- AUTH ---------------- */
  const [token, setToken] = useState<string | null>(null);
  const [userEmail, setUserEmail] = useState("");

  /* ---------------- PRODUCTS ---------------- */
  const [products, setProducts] = useState<Product[]>([]);
  const [allProducts, setAllProducts] = useState<Product[]>([]);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);

  const [searchResults, setSearchResults] = useState<Product[]>([]);
  const [searching, setSearching] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);

  /* ---------------- CART / WISHLIST ---------------- */
  const [cartItems, setCartItems] = useState<CartItem[]>([]);
  const [wishlist, setWishlist] = useState<Product[]>([]);

  /* ---------------- UI ---------------- */
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [loading, setLoading] = useState(true);

  const [activePanel, setActivePanel] = useState<
    | "home"
    | "products_all"
    | "cart"
    | "wishlist"
    | "orders"
    | "notifications"
    | "payment"
    | "chat"
    | "profile"
    | "quotations"
    | "about"
  >("home");

  /* ---------------- INITIAL LOAD ---------------- */
  useEffect(() => {
    const init = async () => {
      try {
        const stored = localStorage.getItem("access_token");
        setToken(stored);

        if (!stored) return setLoading(false);

        const profile = await getProfile(stored);
        setUserEmail(profile.email || "");

        await fetchProductsHandler(1, stored); //  PASS TOKEN


        setCartItems(normalizeCart(await getCart(stored)));
        setWishlist(
          normalizeProducts(
            (await getWishlist(stored)).map((w: WishlistItem) => w.product)
          )
        );
        setNotifications(await getNotifications(stored));
      } finally {
        setLoading(false);
      }
    };
    init();
  }, []);

  /* ---------------- PRODUCTS ---------------- */
const fetchProductsHandler = async (pageNo = 1, authToken?: string) => {
  const activeToken = authToken || token;
  if (!activeToken) return;

  const data = await getProducts(activeToken, { page: pageNo });
  const normalized = normalizeProducts(data.results);

  if (pageNo === 1) {
    setProducts(normalized.slice(0, 20));
    setAllProducts(normalized);
  } else {
    setAllProducts((p) => [...p, ...normalized]);
  }

  setHasMore(Boolean(data.next));
  setPage(pageNo);
};


  /* ---------------- CART ---------------- */
  const refreshCart = async () => {
    if (!token) return;
    setCartItems(normalizeCart(await getCart(token)));
  };

  const handleAddToCart = async (
    product: Product,
    qty: number,
    variant?: ProductVariant
  ) => {
    if (!token) return alert("Login required");
    await addToCart(token, product.id, qty, variant?.id ?? null);
    await refreshCart();
  };

  /* ---------------- WISHLIST ---------------- */
  const handleToggleWishlist = async (product: Product) => {
    if (!token) return alert("Login required");
    await toggleWishlist(
      token,
      product.id.toString(),
      wishlist.some((w) => w.id === product.id)
    );
    setWishlist(
      normalizeProducts(
        (await getWishlist(token)).map((w: WishlistItem) => w.product)
      )
    );
  };

  /* ---------------- SEARCH ---------------- */
  const handleSearch = async (q: string) => {
    if (!q) {
      setSearching(false);
      setActivePanel("home");
      return;
    }

    const cat = CATEGORIES.find((c) => c.key.toLowerCase() === q.toLowerCase());
    const data = cat
      ? await getProducts(token!, { category: cat.key })
      : await searchProducts(q);

    setSearchResults(normalizeProducts(cat ? data.results : data));
    setSearching(true);
  };

  /* ---------------- BODY ---------------- */
  let bodyContent: JSX.Element | null = null;

  if (selectedProduct) {
    bodyContent = (
      <ProductDetails
        product={selectedProduct}
        onBack={() => setSelectedProduct(null)}
        onAddToCart={handleAddToCart}
        token={token!}
      />
    );
  } else if (activePanel === "products_all") {
    bodyContent = (
      <DisplayProducts
        title="All Products"
        products={allProducts}
        hasMore={hasMore}
        onLoadMore={() => fetchProductsHandler(page + 1)}
        onSelectProduct={setSelectedProduct}
        onToggleWishlist={handleToggleWishlist}
        wishlist={wishlist}
        onClose={() => setActivePanel("home")}   //  CLOSE BUTTON WORKS

      />
    );
  } else if (activePanel === "cart") {
    bodyContent = (
      <CartPanel
        products={cartItems}
        onClose={() => setActivePanel("home")}
        onCheckout={() => setActivePanel("payment")}
        onUpdateQuantity={async (id, pid, q) => {
          q <= 0
            ? await removeCartItem(token!, id)
            : await addToCart(token!, pid, q);
          refreshCart();
        }}
        onClearItem={async (id) => {
          await clearCartItem(token!, id);
          refreshCart();
        }}
      />
    );
  } else if (activePanel === "wishlist") {
    bodyContent = (
      <WishlistPanel
        products={wishlist}
        onClose={() => setActivePanel("home")}
        onAddToCart={handleAddToCart}
        onToggleWishlist={handleToggleWishlist}
        onSelectProduct={setSelectedProduct}
      />
    );
  } else if (activePanel === "orders") {
    bodyContent = (
      <OrdersList
        token={token!}
        onClose={() => setActivePanel("home")}
        onStartChat={() => setActivePanel("chat")}
      />
    );
  } else if (activePanel === "notifications") {
    bodyContent = (
      <NotificationsPanel
        notifications={notifications}
        title="Notifications"
        onClose={() => setActivePanel("home")}
        onMarkRead={async (id) => {
          await markNotificationRead(token!, id);
          setNotifications(await getNotifications(token!));
        }}
      />
    );
  } else if (activePanel === "payment") {
    bodyContent = (
      <PaymentPanel
        token={token!}
        cartItems={cartItems}
        onClose={() => setActivePanel("home")}
        onOrderPlaced={() => setActivePanel("orders")}
      />
    );
  } else if (activePanel === "chat") {
    bodyContent = (
      <Chat token={token!} userEmail={userEmail} onClose={() => setActivePanel("home")} />
    );
  } else if (activePanel === "profile") {
    bodyContent = (
      <UserProfile token={token!} onClose={() => setActivePanel("home")} />
    );
  } else if (activePanel === "quotations") {
    bodyContent = <QuotationsPanel token={token!} onClose={() => setActivePanel("home")} />;
  } else if (activePanel === "about") {
    bodyContent = <About />;
  } else if (searching) {
    bodyContent = (
      <div className="relative w-full">
        <button
          onClick={() => setSearching(false)}
          className="absolute top-3 right-3"
        >
          <X size={26} />
        </button>
        <ProductsList
          title="Search Results"
          products={searchResults}
          onSelectProduct={setSelectedProduct}
          onToggleWishlist={handleToggleWishlist}
          wishlist={wishlist}
        />
      </div>
    );
  } else {
    bodyContent = (
      <div className="w-[90%] max-w-[1440px] mx-auto">
        <BannerAd />
        <RecentlyViewed
          token={token!}
          onSelectProduct={setSelectedProduct}
          onToggleWishlist={handleToggleWishlist}
          wishlist={wishlist}
          onSeeAll={() => {
            setActivePanel("products_all");
            fetchProductsHandler(1);
          }}
        />

        <ProductsList
          title="Recommended for You"
          products={products} //  all products
          onSelectProduct={setSelectedProduct}
          onToggleWishlist={handleToggleWishlist}
          wishlist={wishlist}
          onSeeAll={() => {
            setActivePanel("products_all");
            fetchProductsHandler(1);
          }}
        />

      </div>
    );
  }

  if (loading) return <div className="h-screen flex items-center">Loading...</div>;

  return (
    <div className="min-h-screen bg-[#fafafa]">
      <Header
        token={token}
        onSearch={handleSearch}
        cartCount={cartItems.reduce((s, i) => s + i.quantity, 0)}
        notificationCount={notifications.filter((n) => !n.is_read).length}
        onCartPress={() => setActivePanel("cart")}
        onWishlistPress={() => setActivePanel("wishlist")}
        onOrdersPress={() => setActivePanel("orders")}
        onNotificationsPress={() => setActivePanel("notifications")}
        onUserPress={() => setActivePanel("profile")}
        onChatPress={() => setActivePanel("chat")}
        onQuotationsPress={() => setActivePanel("quotations")}
        onAboutPress={() => setActivePanel("about")}
        onLogout={() => {
          localStorage.clear();
          navigate("/login");
        }}
      />
      {bodyContent}
    </div>
  );
}
