import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

// UPDATED API IMPORTS
import {
  registerBuyer,
  registerSeller,
  verifyOTPRegister,
} from "../api/api";

import {
  FullRegisterForm,
  Role,
  BuyerRegisterPayload,
  BuyerOTPVerifyPayload,
} from "../types";

const SELLER_BUSINESS_CATEGORIES: string[] = [
  "Chemicals",
  "Instruments",
  "Consumables",
  "PPE",
  "E-Devices",
  "Glassware",
  "Biologics",
  "Solvents",
  "Books & Stationery",
  "Furniture",
];

const BUYER_CATEGORIES: string[] = [
  "University",
  "Research Lab",
  "Corporate R&D",
  "Medical / Pharma",
  "Student",
  "Government Org",
  "Other",
];

const ENTITY_TYPES: string[] = [
  "Proprietorship",
  "Partnership",
  "Private Limited",
  "Public Limited",
  "LLP",
  "Other",
];

const DESIGNATIONS: string[] = [
  "Owner / Founder",
  "Director",
  "Manager",
  "Procurement",
  "Other",
];

const STATES: string[] = [
  "Select state",
  "Andhra Pradesh",
  "Delhi",
  "Gujarat",
  "Karnataka",
  "Maharashtra",
  "Tamil Nadu",
  "Uttar Pradesh",
  "West Bengal",
];

const CITIES: string[] = [
  "Select city",
  "Mumbai",
  "Delhi",
  "Bengaluru",
  "Chennai",
  "Hyderabad",
  "Ahmedabad",
  "Pune",
  "Kolkata",
];

export default function Register() {
  const navigate = useNavigate();

  const [form, setForm] = useState<FullRegisterForm>({
    role: "buyer",

    // common
    name: "",
    phone: "",
    email: "",

    // buyer
    username: "",
    password: "",
    confirm_password: "",
    buyerCategory: "",
    organizationName: "",
    city: "",
    state: "",
    pincode: "",
    otp: "", // not used directly, but present in type

    // seller
    businessName: "",
    entityType: "",
    gstNumber: "",
    panNumber: "",
    businessRegDoc: null,
    gstCertificate: null,
    designation: "",
    addressLine1: "",
    addressLine2: "",
    sellerCategories: [],
    websiteUrl: "",
    linkedinUrl: "",
  });

  const [errors, setErrors] = useState<Record<string, string>>({});
  const [loading, setLoading] = useState(false);

  // OTP states
  const [buyerOtp, setBuyerOtp] = useState("");
  const [buyerOtpSent, setBuyerOtpSent] = useState(false);
  const [buyerOtpVerified, setBuyerOtpVerified] = useState(false);

  const [showPassword, setShowPassword] = useState(false);

  const isBuyer = form.role === "buyer";
  const isSeller = form.role === "seller";

  // -------------------------------
  // FIELD HANDLERS
  // -------------------------------
  const handleChange = <K extends keyof FullRegisterForm>(
    field: K,
    value: FullRegisterForm[K]
  ) => {
    setForm((prev) => ({ ...prev, [field]: value }));
    if (errors[field as string]) {
      setErrors((prev) => ({ ...prev, [field as string]: "" }));
    }
  };

  const handleInputChange =
    (field: keyof FullRegisterForm) =>
    (e: React.ChangeEvent<HTMLInputElement>) => {
      handleChange(field, e.target.value as any);
    };

  const handleSelectChange =
    (field: keyof FullRegisterForm) =>
    (e: React.ChangeEvent<HTMLSelectElement>) => {
      handleChange(field, e.target.value as any);
    };

  const handleFileChange = (
    field: "businessRegDoc" | "gstCertificate",
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const file = e.target.files?.[0] || null;
    setForm((prev) => ({ ...prev, [field]: file }));
  };

  const toggleSellerCategory = (category: string) => {
    setForm((prev) => {
      const exists = prev.sellerCategories.includes(category);
      return {
        ...prev,
        sellerCategories: exists
          ? prev.sellerCategories.filter((c) => c !== category)
          : [...prev.sellerCategories, category],
      };
    });
  };

  // -------------------------------
  // BUYER VALIDATION
  // -------------------------------
  const validateBuyer = () => {
    const newErrors: Record<string, string> = {};

    if (!form.name.trim()) newErrors.name = "Please enter full name";
    if (!form.email.trim()) newErrors.email = "Email is required";
    if (!/^\S+@\S+\.\S+$/.test(form.email))
      newErrors.email = "Enter a valid email";

    if (!form.phone.trim()) newErrors.phone = "Phone is required";
    if (!form.username.trim()) newErrors.username = "Username is required";

    if (!form.password) newErrors.password = "Password is required";
    else if (form.password.length < 6)
      newErrors.password = "Minimum 6 characters";

    if (form.password !== form.confirm_password)
      newErrors.confirm_password = "Passwords do not match";

    if (!form.buyerCategory.trim())
      newErrors.buyerCategory = "Select buyer category";

    if (!form.organizationName.trim())
      newErrors.organizationName = "Organization name required";

    if (!form.city.trim()) newErrors.city = "Select city";
    if (!form.state.trim()) newErrors.state = "Select state";
    if (!form.pincode.trim()) newErrors.pincode = "Pincode is required";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // -------------------------------
  // SELLER VALIDATION
  // -------------------------------
  const validateSeller = () => {
    const newErrors: Record<string, string> = {};

    if (!form.businessName.trim())
      newErrors.businessName = "Business name is required";
    if (!form.entityType.trim())
      newErrors.entityType = "Select type of entity";
    if (!form.gstNumber.trim()) newErrors.gstNumber = "GST is required";
    if (!form.panNumber.trim()) newErrors.panNumber = "PAN is required";

    if (!form.name.trim())
      newErrors.name = "Contact person name is required";
    if (!form.phone.trim())
      newErrors.phone = "Contact mobile number is required";
    if (!form.email.trim()) newErrors.email = "Email is required";
    if (!/^\S+@\S+\.\S+$/.test(form.email))
      newErrors.email = "Enter a valid email";

    if (!form.addressLine1.trim())
      newErrors.addressLine1 = "Address Line 1 is required";

    if (!form.city.trim()) newErrors.city = "Select city";
    if (!form.state.trim()) newErrors.state = "Select state";
    if (!form.pincode.trim()) newErrors.pincode = "Pincode is required";

    if (form.sellerCategories.length === 0)
      newErrors.sellerCategories = "Select at least one business category";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // -------------------------------
  // BUYER  SEND OTP
  // -------------------------------
  const handleBuyerSendOtp = async () => {
    if (!validateBuyer()) return;

    setLoading(true);
    try {
      const payload: BuyerRegisterPayload = {
        name: form.name,
        phone: form.phone,
        email: form.email,
        password: form.password,
        confirm_password: form.confirm_password,
        role: "buyer",
      };

      await registerBuyer(payload);

      setBuyerOtpSent(true);
      alert(" OTP sent to your email!");
    } catch (err: any) {
      alert(err?.message || "Failed to send OTP");
    } finally {
      setLoading(false);
    }
  };

  // -------------------------------
  // BUYER  VERIFY OTP
  // -------------------------------
  const handleBuyerVerifyOtp = async () => {
    if (!buyerOtp.trim()) return alert("Enter OTP");

    setLoading(true);
    try {
      const payload: BuyerOTPVerifyPayload = {
        email: form.email,
        otp: buyerOtp,
        username: form.username,
        buyerCategory: form.buyerCategory,
        organizationName: form.organizationName,
        city: form.city,
        state: form.state,
        pincode: form.pincode,
      };

      await verifyOTPRegister(payload);

      setBuyerOtpVerified(true);

      alert(" Account created successfully!");
      navigate("/login");
    } catch (err: any) {
      alert(err?.message || "Failed to verify OTP");
    } finally {
      setLoading(false);
    }
  };

  // -------------------------------
  // SELLER  SUBMIT (NO OTP)
  // -------------------------------
  const handleSellerSubmit = async () => {
    if (!validateSeller()) return;

    setLoading(true);
    try {
      const fd = new FormData();

      // common
      fd.append("name", form.name);
      fd.append("phone", form.phone);
      fd.append("email", form.email);
      fd.append("role", "seller");

      // business
      fd.append("businessName", form.businessName);
      fd.append("entityType", form.entityType);
      fd.append("gstNumber", form.gstNumber);
      fd.append("panNumber", form.panNumber);

      // address
      fd.append("addressLine1", form.addressLine1);
      fd.append("addressLine2", form.addressLine2);
      fd.append("city", form.city);
      fd.append("state", form.state);
      fd.append("pincode", form.pincode);

      // categories
      form.sellerCategories.forEach((cat) =>
        fd.append("sellerCategories", cat)
      );

      // optional
      if (form.designation) fd.append("designation", form.designation);
      if (form.websiteUrl) fd.append("websiteUrl", form.websiteUrl);
      if (form.linkedinUrl) fd.append("linkedinUrl", form.linkedinUrl);

      // files
      if (form.businessRegDoc)
        fd.append("businessRegDoc", form.businessRegDoc);
      if (form.gstCertificate)
        fd.append("gstCertificate", form.gstCertificate);

      await registerSeller(fd);

      alert(" Seller submitted for approval!");
      navigate("/login");
    } catch (err: any) {
      alert(err?.message || "Failed to submit seller registration");
    } finally {
      setLoading(false);
    }
  };

  // -------------------------------
  // MAIN SUBMIT HANDLER
  // -------------------------------
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (isSeller) return handleSellerSubmit();

    if (!buyerOtpSent) return handleBuyerSendOtp();

    if (!buyerOtpVerified) return handleBuyerVerifyOtp();
  };

  // -------------------------------
  // ROLE SWITCH
  // -------------------------------
  const handleRoleChange = (role: Role) => {
    setForm((prev) => ({ ...prev, role }));
    setErrors({});
  };

  // ------------------------------------------------------
  // UI
  // ------------------------------------------------------
  return (
    <div className="min-h-screen bg-gray-50 flex flex-col">
      <header className="w-full border-b bg-white">
        <div className="max-w-5xl mx-auto flex items-center justify-between px-4 py-3">
          <div className="flex items-center gap-3">
            <img
              src="images/Linkzur_web.jpg"
              alt="Linkzur Logo"
              className="h-full max-h-12 w-auto md:max-h-16 object-contain"
            />
          </div>
          <div className="text-sm">
            <span className="text-gray-600 mr-2">Already have an account?</span>
            <button
              onClick={() => navigate("/login")}
              className="inline-flex items-center rounded-full border border-blue-600 px-4 py-1.5 text-xs font-semibold text-blue-600 hover:bg-blue-50"
            >
              Log in
            </button>
          </div>
        </div>
      </header>

      <main className="flex-1 flex items-center justify-center px-4 py-6">
        <div className="w-full max-w-5xl bg-white shadow-md rounded-xl border border-gray-200 px-6 py-6 md:px-10 md:py-8">

          {/* Role toggle */}
          <div className="flex justify-center mb-4">
            <div className="inline-flex bg-gray-100 rounded-full p-1">
              <button
                type="button"
                onClick={() => handleRoleChange("buyer")}
                className={`px-6 py-1.5 text-sm font-medium rounded-full transition ${
                  isBuyer
                    ? "bg-blue-600 text-white shadow"
                    : "text-gray-600 hover:text-gray-900"
                }`}
              >
                Buyer
              </button>
              <button
                type="button"
                onClick={() => handleRoleChange("seller")}
                className={`px-6 py-1.5 text-sm font-medium rounded-full transition ${
                  isSeller
                    ? "bg-blue-600 text-white shadow"
                    : "text-gray-600 hover:text-gray-900"
                }`}
              >
                Seller
              </button>
            </div>
          </div>

          <div className="text-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-900">
              {isBuyer ? "Buyer Registration" : "Seller Registration"}
            </h2>
          </div>

          <form onSubmit={handleSubmit} className="space-y-8">

            {/* BUYER FORM */}
            {isBuyer && (
              <>
                <div className="max-w-xl mx-auto space-y-4">

                  {/* Full Name */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Full Name *
                    </label>
                    <input
                      type="text"
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.name}
                      onChange={handleInputChange("name")}
                    />
                    {errors.name && (
                      <p className="text-xs text-red-500">{errors.name}</p>
                    )}
                  </div>

                  {/* Email */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Email Address *
                    </label>
                    <input
                      type="email"
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.email}
                      onChange={handleInputChange("email")}
                    />
                    {errors.email && (
                      <p className="text-xs text-red-500">{errors.email}</p>
                    )}
                  </div>

                  {/* Phone */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Mobile Number *
                    </label>
                    <div className="flex gap-2">
                      <select className="rounded-lg border border-gray-300 px-2 py-2 text-sm">
                        <option value="+91">+91</option>
                      </select>
                      <input
                        type="tel"
                        className="flex-1 rounded-lg border border-gray-300 px-3 py-2 text-sm"
                        value={form.phone}
                        onChange={handleInputChange("phone")}
                      />
                    </div>
                    {errors.phone && (
                      <p className="text-xs text-red-500">{errors.phone}</p>
                    )}
                  </div>

                  {/* Username */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Username *
                    </label>
                    <input
                      type="text"
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.username}
                      onChange={handleInputChange("username")}
                    />
                    {errors.username && (
                      <p className="text-xs text-red-500">
                        {errors.username}
                      </p>
                    )}
                  </div>

                  {/* Password */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Password *
                    </label>
                    <div className="relative">
                      <input
                        type={showPassword ? "text" : "password"}
                        className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm pr-10"
                        value={form.password}
                        onChange={handleInputChange("password")}
                      />
                      <button
                        type="button"
                        onClick={() => setShowPassword((s) => !s)}
                        className="absolute right-3 text-xs text-gray-500 top-1/2 -translate-y-1/2"
                      >
                        {showPassword ? "Hide" : "Show"}
                      </button>
                    </div>
                    {errors.password && (
                      <p className="text-xs text-red-500">
                        {errors.password}
                      </p>
                    )}
                  </div>

                  {/* Confirm Password */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Confirm Password *
                    </label>
                    <input
                      type={showPassword ? "text" : "password"}
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.confirm_password}
                      onChange={handleInputChange("confirm_password")}
                    />
                    {errors.confirm_password && (
                      <p className="text-xs text-red-500">
                        {errors.confirm_password}
                      </p>
                    )}
                  </div>

                  {/* Buyer Category */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Buyer Category *
                    </label>
                    <select
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.buyerCategory}
                      onChange={handleSelectChange("buyerCategory")}
                    >
                      <option value="">Select category</option>
                      {BUYER_CATEGORIES.map((c) => (
                        <option key={c}>{c}</option>
                      ))}
                    </select>
                    {errors.buyerCategory && (
                      <p className="text-xs text-red-500">
                        {errors.buyerCategory}
                      </p>
                    )}
                  </div>

                  {/* Organization */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Organization Name *
                    </label>
                    <input
                      type="text"
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.organizationName}
                      onChange={handleInputChange("organizationName")}
                    />
                    {errors.organizationName && (
                      <p className="text-xs text-red-500">
                        {errors.organizationName}
                      </p>
                    )}
                  </div>

                  {/* City */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      City *
                    </label>
                    <select
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.city}
                      onChange={handleSelectChange("city")}
                    >
                      <option value="">Select city</option>
                      {CITIES.map((c) => (
                        <option key={c}>{c}</option>
                      ))}
                    </select>
                    {errors.city && (
                      <p className="text-xs text-red-500">{errors.city}</p>
                    )}
                  </div>

                  {/* State */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      State *
                    </label>
                    <select
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.state}
                      onChange={handleSelectChange("state")}
                    >
                      <option value="">Select state</option>
                      {STATES.map((s) => (
                        <option key={s}>{s}</option>
                      ))}
                    </select>
                    {errors.state && (
                      <p className="text-xs text-red-500">{errors.state}</p>
                    )}
                  </div>

                  {/* Pincode */}
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Pincode *
                    </label>
                    <input
                      type="text"
                      className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      value={form.pincode}
                      onChange={handleInputChange("pincode")}
                    />
                    {errors.pincode && (
                      <p className="text-xs text-red-500">{errors.pincode}</p>
                    )}
                  </div>
                </div>

                {/* OTP Section - LAST */}
                <div className="max-w-xl mx-auto mt-6">
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Email Verification (OTP)
                    {buyerOtpVerified && (
                      <span className="text-green-600 ml-2"> Verified</span>
                    )}
                  </label>

                  <div className="flex gap-2">
                    <input
                      type="text"
                      maxLength={6}
                      className="flex-1 rounded-lg border border-gray-300 px-3 py-2 text-sm"
                      placeholder="123456"
                      value={buyerOtp}
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                        setBuyerOtp(e.target.value)
                      }
                    />
                    <button
                      type="button"
                      disabled={loading}
                      onClick={
                        !buyerOtpSent
                          ? handleBuyerSendOtp
                          : handleBuyerVerifyOtp
                      }
                      className="rounded-lg bg-blue-600 px-4 py-2 text-sm text-white"
                    >
                      {loading
                        ? "Please wait..."
                        : !buyerOtpSent
                        ? "Send OTP"
                        : buyerOtpVerified
                        ? "Verified"
                        : "Verify OTP"}
                    </button>
                  </div>
                </div>
              </>
            )}

            {/* SELLER FORM */}
            {isSeller && (
              <>
                {/* Business Information */}
                <section>
                  <h3 className="text-lg font-semibold mb-4">
                    Business Information
                  </h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">

                    {/* Business Name */}
                    <div>
                      <label className="text-sm font-medium">
                        Business Name *
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.businessName}
                        onChange={handleInputChange("businessName")}
                      />
                      {errors.businessName && (
                        <p className="text-xs text-red-500">
                          {errors.businessName}
                        </p>
                      )}
                    </div>

                    {/* Entity Type */}
                    <div>
                      <label className="text-sm font-medium">
                        Entity Type *
                      </label>
                      <select
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.entityType}
                        onChange={handleSelectChange("entityType")}
                      >
                        <option value="">Select entity type</option>
                        {ENTITY_TYPES.map((t) => (
                          <option key={t}>{t}</option>
                        ))}
                      </select>
                      {errors.entityType && (
                        <p className="text-xs text-red-500">
                          {errors.entityType}
                        </p>
                      )}
                    </div>

                    {/* GST */}
                    <div>
                      <label className="text-sm font-medium">
                        GST Number *
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.gstNumber}
                        onChange={handleInputChange("gstNumber")}
                      />
                      {errors.gstNumber && (
                        <p className="text-xs text-red-500">
                          {errors.gstNumber}
                        </p>
                      )}
                    </div>

                    {/* PAN */}
                    <div>
                      <label className="text-sm font-medium">
                        PAN Number *
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.panNumber}
                        onChange={handleInputChange("panNumber")}
                      />
                      {errors.panNumber && (
                        <p className="text-xs text-red-500">
                          {errors.panNumber}
                        </p>
                      )}
                    </div>

                    {/* Business Registration */}
                    <div>
                      <label className="text-sm font-medium">
                        Business Registration Document
                      </label>
                      <input
                        type="file"
                        onChange={(e) =>
                          handleFileChange("businessRegDoc", e)
                        }
                      />
                    </div>

                    {/* GST Certificate */}
                    <div>
                      <label className="text-sm font-medium">
                        GST Certificate
                      </label>
                      <input
                        type="file"
                        onChange={(e) =>
                          handleFileChange("gstCertificate", e)
                        }
                      />
                    </div>
                  </div>
                </section>

                {/* Contact Person */}
                <section>
                  <h3 className="text-lg font-semibold mb-4">
                    Contact Person Information
                  </h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">

                    <div>
                      <label className="text-sm font-medium">
                        Full Name *
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.name}
                        onChange={handleInputChange("name")}
                      />
                      {errors.name && (
                        <p className="text-xs text-red-500">{errors.name}</p>
                      )}
                    </div>

                    <div>
                      <label className="text-sm font-medium">Mobile *</label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.phone}
                        onChange={handleInputChange("phone")}
                      />
                      {errors.phone && (
                        <p className="text-xs text-red-500">{errors.phone}</p>
                      )}
                    </div>

                    <div>
                      <label className="text-sm font-medium">Email *</label>
                      <input
                        type="email"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.email}
                        onChange={handleInputChange("email")}
                      />
                      {errors.email && (
                        <p className="text-xs text-red-500">{errors.email}</p>
                      )}
                    </div>

                    <div>
                      <label className="text-sm font-medium">
                        Designation
                      </label>
                      <select
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.designation}
                        onChange={handleSelectChange("designation")}
                      >
                        <option value="">Select designation</option>
                        {DESIGNATIONS.map((d) => (
                          <option key={d}>{d}</option>
                        ))}
                      </select>
                    </div>
                  </div>
                </section>

                {/* Business Address */}
                <section>
                  <h3 className="text-lg font-semibold mb-4">
                    Business Address
                  </h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">

                    <div className="md:col-span-2">
                      <label className="text-sm font-medium">
                        Address Line 1 *
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.addressLine1}
                        onChange={handleInputChange("addressLine1")}
                      />
                      {errors.addressLine1 && (
                        <p className="text-xs text-red-500">
                          {errors.addressLine1}
                        </p>
                      )}
                    </div>

                    <div className="md:col-span-2">
                      <label className="text-sm font-medium">
                        Address Line 2
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.addressLine2}
                        onChange={handleInputChange("addressLine2")}
                      />
                    </div>

                    <div>
                      <label className="text-sm font-medium">City *</label>
                      <select
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.city}
                        onChange={handleSelectChange("city")}
                      >
                        <option value="">Select city</option>
                        {CITIES.map((c) => (
                          <option key={c}>{c}</option>
                        ))}
                      </select>
                      {errors.city && (
                        <p className="text-xs text-red-500">{errors.city}</p>
                      )}
                    </div>

                    <div>
                      <label className="text-sm font-medium">State *</label>
                      <select
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.state}
                        onChange={handleSelectChange("state")}
                      >
                        <option value="">Select state</option>
                        {STATES.map((s) => (
                          <option key={s}>{s}</option>
                        ))}
                      </select>
                      {errors.state && (
                        <p className="text-xs text-red-500">{errors.state}</p>
                      )}
                    </div>

                    <div>
                      <label className="text-sm font-medium">Pincode *</label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.pincode}
                        onChange={handleInputChange("pincode")}
                      />
                      {errors.pincode && (
                        <p className="text-xs text-red-500">{errors.pincode}</p>
                      )}
                    </div>
                  </div>
                </section>

                {/* Business Categories */}
                <section>
                  <h3 className="text-lg font-semibold mb-2">
                    Business Categories *
                  </h3>
                  <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
                    {SELLER_BUSINESS_CATEGORIES.map((cat) => {
                      const checked = form.sellerCategories.includes(cat);
                      return (
                        <label
                          key={cat}
                          className="flex items-center gap-2 text-sm"
                        >
                          <input
                            type="checkbox"
                            checked={checked}
                            onChange={() => toggleSellerCategory(cat)}
                            className="h-4 w-4"
                          />
                          {cat}
                        </label>
                      );
                    })}
                  </div>
                  {errors.sellerCategories && (
                    <p className="text-xs text-red-500">
                      {errors.sellerCategories}
                    </p>
                  )}
                </section>

                {/* Online presence */}
                <section>
                  <h3 className="text-lg font-semibold mb-4">
                    Online Presence (Optional)
                  </h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">

                    <div>
                      <label className="text-sm font-medium">
                        Website URL
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.websiteUrl}
                        onChange={handleInputChange("websiteUrl")}
                      />
                    </div>

                    <div>
                      <label className="text-sm font-medium">
                        LinkedIn / Business Page
                      </label>
                      <input
                        type="text"
                        className="w-full border rounded-lg px-3 py-2"
                        value={form.linkedinUrl}
                        onChange={handleInputChange("linkedinUrl")}
                      />
                    </div>
                  </div>
                </section>
              </>
            )}

            {/* SUBMIT */}
            <div className="pt-2 max-w-xl mx-auto">
              <button
                type="submit"
                disabled={loading || (isBuyer && buyerOtpVerified)}
                className="w-full rounded-lg bg-blue-600 text-white text-sm font-semibold py-3 mt-4 transition disabled:opacity-60"
              >
                {loading
                  ? "Please wait..."
                  : isSeller
                  ? "Submit for Approval"
                  : buyerOtpVerified
                  ? "Account Created!"
                  : !buyerOtpSent
                  ? "Create Account & Send OTP"
                  : "Verify OTP & Complete"}
              </button>
            </div>

          </form>
        </div>
      </main>
    </div>
  );
}


