import React, { useState, KeyboardEvent, useRef, useEffect } from "react";
import {
  Search,
  ShoppingCart,
  Bell,
  User,
  ChevronDown,
  Menu,
  Heart,
} from "lucide-react";
import { useNavigate } from "react-router-dom";

import { CATEGORIES } from "../../constants/Lists";

type HeaderProps = {
  token?: string | null;
  onUserPress: () => void;
  onCartPress: () => void;
  onSearch: (query: string) => void;
  cartCount?: number;
  onWishlistPress: () => void;
  onOrdersPress: () => void;
  onChatPress: () => void;
  onQuotationsPress: () => void;
  onAboutPress: () => void;
  onLogout: () => void;

  onNotificationsPress: () => void;        //  NEW
  notificationCount?: number;              //  NEW
};

export default function BuyerHeader({
  token,
  onUserPress,
  onCartPress,
  onSearch,
  cartCount = 0,
  onWishlistPress,
  onOrdersPress,
  onChatPress,
  onQuotationsPress,
  onAboutPress,
  onLogout,
  onNotificationsPress,        // NEW
  notificationCount = 0,       // NEW
}: HeaderProps) {
  const navigate = useNavigate();

  const [searchText, setSearchText] = useState("");
  const [showDropdown, setShowDropdown] = useState(false);
  const [mobileSearchOpen, setMobileSearchOpen] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const dropdownRef = useRef<HTMLDivElement>(null);

  // Close dropdown when clicking outside
  useEffect(() => {
    const listener = (e: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(e.target as Node)
      ) {
        setShowDropdown(false);
      }
    };
    document.addEventListener("mousedown", listener);
    return () => document.removeEventListener("mousedown", listener);
  }, []);

  const handleKeyPress = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") onSearch(searchText.trim());
  };

  return (
    <>
      {/* MAIN HEADER */}
      <header className="w-full sticky top-0 z-50 bg-white shadow flex items-center justify-between px-4 lg:px-10 h-16">

        {/* LEFT: MENU + LOGO */}
        <div className="flex items-center gap-3">

          {/* Mobile Menu */}
          <Menu
            className="w-6 h-6 text-gray-800 lg:hidden cursor-pointer"
            onClick={() => setSidebarOpen(true)}
          />



          {/* Desktop Logo */}
          <img
            src="/images/Linkzur_web.jpg"
            alt="desktop-logo"
            onClick={() => navigate("/buyer/home")}
            className="h-full max-h-16 w-auto md:max-h-16 object-contain"
          />
        </div>

        {/* DESKTOP SEARCH BAR */}
        <div className="hidden lg:flex items-center gap-2 w-[45%]">
          <div className="flex items-center bg-gray-100 rounded-full px-4 py-2 w-full">
            <Search className="w-5 h-5 text-gray-500" />
            <input
              placeholder="Search for anything..."
              className="bg-transparent ml-2 w-full focus:outline-none"
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
              onKeyDown={handleKeyPress}
            />

            {/* Category dropdown */}
            <select
              className="bg-transparent text-sm text-gray-600 outline-none cursor-pointer"
              onChange={(e) => onSearch(e.target.value)}
            >
              <option value="">All Categories</option>
              {CATEGORIES.map((c) => (
                <option key={c.key} value={c.key}>
                  {c.label}
                </option>
              ))}
            </select>
          </div>

          <button
            className="bg-blue-600 text-white px-5 py-2 rounded-full hover:bg-blue-700"
            onClick={() => onSearch(searchText.trim())}
          >
            Search
          </button>
        </div>

        {/* RIGHT SIDE ICONS */}
        <div className="flex items-center gap-4">

          {/* Mobile Search Icon */}
          <button
            className="lg:hidden text-gray-700"
            onClick={() => setMobileSearchOpen(!mobileSearchOpen)}
          >
            <Search className="w-6 h-6" />
          </button>

          {/* Cart */}
          <button className="relative" onClick={onCartPress}>
            <ShoppingCart className="w-6 h-6 text-gray-700" />
            {cartCount > 0 && (
              <span className="absolute -top-1 -right-2 bg-red-600 text-white text-xs px-2 py-0.5 rounded-full">
                {cartCount}
              </span>
            )}
          </button>

          {/* If not logged in */}
          {!token ? (
            <div className="hidden lg:flex items-center gap-4">
              <button onClick={() => navigate("/login")} className="font-semibold">
                Login
              </button>
              <button
                onClick={() => navigate("/register")}
                className="bg-blue-600 text-white px-4 py-2 rounded-full hover:bg-blue-700"
              >
                Sign Up
              </button>
              <button
                onClick={onAboutPress}
                className="bg-blue-600 text-white px-4 py-2 rounded-full hover:bg-blue-700"
              >
                About Us
              </button>
            </div>
          ) : (
            <>
              {/* Notifications Button */}
              <button
                className="hidden lg:block relative text-gray-700"
                onClick={onNotificationsPress}
              >
                <Bell className="w-6 h-6" />

                {/*  Badge */}
                {notificationCount > 0 && (
                  <span className="absolute -top-1 -right-2 bg-red-600 text-white text-[10px] px-1.5 py-0.5 rounded-full">
                    {notificationCount}
                  </span>
                )}
              </button>

              {/* DROPDOWN MENU */}
              <div className="hidden lg:block relative" ref={dropdownRef}>
                <button
                  className="flex items-center gap-1 text-gray-800"
                  onClick={() => setShowDropdown(!showDropdown)}
                >
                  <User className="w-6 h-6" />
                  <ChevronDown className="w-4 h-4" />
                </button>

                {showDropdown && (
                  <div className="absolute right-0 mt-2 w-48 bg-white shadow-lg border rounded-lg py-2 z-50">
                    <button
                      onClick={onUserPress}
                      className="w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                    >
                      My Profile
                    </button>

                    <button
                      onClick={onWishlistPress}
                      className="w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                    >
                      Wishlist
                    </button>

                    <button
                      onClick={onOrdersPress}
                      className="w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                    >
                      Orders
                    </button>

                    <button
                      onClick={onChatPress}
                      className="w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                    >
                      Chat
                    </button>

                    <button
                      onClick={onQuotationsPress}
                      className="w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                    >
                      Quotations
                    </button>

                    <button
                      onClick={onAboutPress}
                      className="w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                    >
                      About Us
                    </button>

                    <hr className="my-2" />

                    <button
                      onClick={onLogout}
                      className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50"
                    >
                      Logout
                    </button>
                  </div>
                )}
              </div>
            </>
          )}
        </div>
      </header>

      {/* ---------- MOBILE SEARCH BAR ---------- */}
      {mobileSearchOpen && (
      <div className="lg:hidden w-full bg-white border-b px-4 py-3">
        <div className="flex items-center bg-gray-100 rounded-full px-4 py-2">
          <Search className="w-5 h-5 text-gray-500" />

          <input
            placeholder="Search..."
            className="w-full ml-2 bg-transparent focus:outline-none"
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
            onKeyDown={handleKeyPress}
          />

          {/*  Mobile Search Button */}
          <button
            onClick={() => onSearch(searchText.trim())}
            className="ml-2 text-blue-600 font-semibold"
          >
            Go
          </button>
        </div>
      </div>
    )}

      {/* ---------- MOBILE SIDEBAR ---------- */}
      {sidebarOpen && (
        <>
          {/* overlay */}
          <div
            className="fixed inset-0 bg-black/40 z-40"
            onClick={() => setSidebarOpen(false)}
          />

          {/* sidebar */}
          <div className="fixed top-0 left-0 h-full w-72 bg-white shadow-xl z-50 p-5 animate-slide">

            <div className="flex justify-between items-center mb-5">
              <h3 className="text-lg font-semibold">Menu</h3>
              <button onClick={() => setSidebarOpen(false)}></button>
            </div>

            {!token ? (
              <>
                <button className="sidebar-link" onClick={() => navigate("/login")}>
                  Login
                </button>
                <button className="sidebar-link" onClick={() => navigate("/register")}>
                  Sign Up
                </button>
                <button className="sidebar-link" onClick={onAboutPress}>
                  About Us
                </button>
              </>
            ) : (
              <>
                <button className="sidebar-link" onClick={onUserPress}>
                  My Profile
                </button>
                <button className="sidebar-link" onClick={onWishlistPress}>
                  Wishlist
                </button>
                <button className="sidebar-link" onClick={onOrdersPress}>
                  Orders
                </button>
                <button className="sidebar-link" onClick={onChatPress}>
                  Chat
                </button>
                <button className="sidebar-link" onClick={onQuotationsPress}>
                  Quotations
                </button>
                <button className="sidebar-link" onClick={onNotificationsPress}>
                  Notifications
                </button>
                <button className="sidebar-link" onClick={onAboutPress}>
                  About Us
                </button>
                <button className="sidebar-link text-red-600" onClick={onLogout}>
                  Logout
                </button>
              </>
            )}
          </div>

          {/* animation */}
          <style>{`
            @keyframes slide {
              from { transform: translateX(-100%); }
              to { transform: translateX(0); }
            }
            .animate-slide {
              animation: slide 0.3s ease-out;
            }
            .sidebar-link {
              width: 100%;
              padding: 12px 0;
              text-align: left;
              font-size: 15px;
              border-bottom: 1px solid #eee;
            }
          `}</style>
        </>
      )}
    </>
  );
}
