import React, { useState, useEffect } from "react";

const banners = [
  {
    image: "/images/banners/ban_1.png",
    buttonText: "Explore Us",
  },
  {
    image: "/images/banners/ban_2.png",
    buttonText: "Buy Now",
  },
  {
    image: "/images/banners/ban_3.png",
    buttonText: "Check Deals",
  },
   {
    image: "/images/banners/ban_4.png",
    buttonText: "Check Deals",
  },
   {
    image: "/images/banners/ban_5.png",
    buttonText: "Check Deals",
  },
];

export default function BannerAd() {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % banners.length);
    }, 3000);
    return () => clearInterval(timer);
  }, []);

  return (
    <div className="relative w-full h-[450px] md:h-[420px] overflow-hidden rounded-lg">
      {banners.map((banner, index) => (
        <div
          key={index}
          className={`absolute inset-0 transition-opacity duration-700 ease-in-out 
          ${index === currentIndex ? "opacity-100" : "opacity-0"}`}
        >
          {/* BLURRED BACKGROUND FILL */}
          <img
            src={banner.image}
            alt=""
            className="absolute inset-0 w-full h-full object-cover blur-lg scale-110"
          />

          {/* MAIN IMAGE (FULL, NO CROPPING) */}
          <img
            src={banner.image}
            alt={`banner-${index}`}
            className="relative z-10 w-full h-full object-contain"
          />

          {/* BUTTON */}
          <button
            className="
              absolute bottom-6 left-1/2 -translate-x-1/2 
              z-20 bg-white text-gray-800 font-medium px-6 py-2 rounded-full shadow 
              hover:bg-gray-100 transition
            "
          >
            {banner.buttonText}
          </button>
        </div>
      ))}
    </div>
  );
}
