import React, { useEffect, useState } from "react";
import { getProfile, updateProfile } from "../../api/api";

const ENTITY_TYPES = [
  "Proprietorship",
  "Partnership",
  "Private Limited",
  "Public Limited",
  "LLP",
  "Other",
];

const SELLER_CATEGORIES = [
  "Chemicals",
  "Instruments",
  "Consumables",
  "PPE",
  "E-Devices",
  "Glassware",
  "Biologics",
  "Solvents",
  "Books & Stationery",
  "Furniture",
];

const STATES = [
  "Andhra Pradesh",
  "Delhi",
  "Gujarat",
  "Karnataka",
  "Maharashtra",
  "Tamil Nadu",
  "Uttar Pradesh",
  "West Bengal",
];

const CITIES = [
  "Mumbai",
  "Delhi",
  "Bengaluru",
  "Chennai",
  "Hyderabad",
  "Ahmedabad",
  "Pune",
  "Kolkata",
];

type Props = {
  token: string;
  onClose: () => void;
};

export default function SellerProfile({ token, onClose }: Props) {
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [editMode, setEditMode] = useState(false);

  const [form, setForm] = useState<any>({
    name: "",
    email: "",
    phone: "",

    businessName: "",
    entityType: "",
    gstNumber: "",
    panNumber: "",

    addressLine1: "",
    addressLine2: "",
    city: "",
    state: "",
    pincode: "",

    designation: "",
    websiteUrl: "",
    linkedinUrl: "",

    sellerCategories: [],

    businessRegDoc: null,
    gstCertificate: null,
  });

  // ------------------------------------------
  // FETCH PROFILE
  // ------------------------------------------
  useEffect(() => {
    async function loadData() {
      try {
        const data = await getProfile(token);

        setForm({
          name: data.name || "",
          email: data.email || "",
          phone: data.phone || "",

          businessName: data.businessName || "",
          entityType: data.entityType || "",
          gstNumber: data.gstNumber || "",
          panNumber: data.panNumber || "",

          addressLine1: data.addressLine1 || "",
          addressLine2: data.addressLine2 || "",
          city: data.city || "",
          state: data.state || "",
          pincode: data.pincode || "",

          designation: data.designation || "",
          websiteUrl: data.websiteUrl || "",
          linkedinUrl: data.linkedinUrl || "",

          //  Fix: Always convert to array
          sellerCategories: Array.isArray(data.sellerCategories)
            ? data.sellerCategories
            : typeof data.sellerCategories === "string"
            ? data.sellerCategories.split(",")
            : [],

          businessRegDoc: null,
          gstCertificate: null,
        });
      } catch (err) {
        alert("Failed to load seller profile.");
      } finally {
        setLoading(false);
      }
    }

    loadData();
  }, [token]);

  // ------------------------------------------
  // HANDLERS
  // ------------------------------------------
  const handleChange = (field: string, value: any) => {
    setForm((prev: any) => ({ ...prev, [field]: value }));
  };

  const handleFile = (field: string, file: File | null) => {
    setForm((prev: any) => ({ ...prev, [field]: file }));
  };

  const toggleCategory = (cat: string) => {
    setForm((prev: any) => {
      const exists = prev.sellerCategories.includes(cat);
      return {
        ...prev,
        sellerCategories: exists
          ? prev.sellerCategories.filter((c: string) => c !== cat)
          : [...prev.sellerCategories, cat],
      };
    });
  };

  // ------------------------------------------
  // SAVE PROFILE (FormData)
  // ------------------------------------------
  const handleSave = async () => {
    setSaving(true);

    try {
      const fd = new FormData();

      Object.keys(form).forEach((key) => {
        const value = form[key];

        if (key === "sellerCategories") {
          if (Array.isArray(value)) {
            value.forEach((cat: string) =>
              fd.append("sellerCategories", cat)
            );
          }
        } else if (value !== null && value !== undefined) {
          fd.append(key, value);
        }
      });

      await updateProfile(token, fd);

      alert("Profile updated successfully!");
      setEditMode(false);

    } catch (err: any) {
      alert(err.message || "Update failed.");
    } finally {
      setSaving(false);
    }
  };

  if (loading)
    return <div className="flex justify-center mt-10">Loading...</div>;

  return (
    <div className="w-full max-w-4xl mx-auto bg-white shadow-md rounded-xl p-6 mt-6 border">

      {/* HEADER */}
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold text-gray-800">Seller Profile</h2>

        <button
          onClick={onClose}
          className="px-3 py-1 rounded-lg bg-gray-100 hover:bg-gray-200"
        >
          Close
        </button>
      </div>

      {/* AVATAR */}
      <div className="flex justify-center mb-6">
        <div className="w-20 h-20 rounded-full bg-blue-600 text-white flex items-center justify-center text-3xl font-bold">
          {form.name?.charAt(0).toUpperCase()}
        </div>
      </div>

      {/* ======================
          BASIC INFO
      ====================== */}
      <h3 className="text-lg font-medium mb-3">Personal Information</h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">

        <div>
          <label className="text-sm font-medium">Full Name</label>
          <input
            type="text"
            disabled={!editMode}
            value={form.name}
            onChange={(e) => handleChange("name", e.target.value)}
            className={`mt-1 w-full px-3 py-2 rounded-lg border ${
              editMode ? "bg-white" : "bg-gray-100"
            }`}
          />
        </div>

        <div>
          <label className="text-sm font-medium">Email</label>
          <input
            type="email"
            disabled={!editMode}
            value={form.email}
            onChange={(e) => handleChange("email", e.target.value)}
            className={`mt-1 w-full px-3 py-2 rounded-lg border ${
              editMode ? "bg-white" : "bg-gray-100"
            }`}
          />
        </div>

        <div>
          <label className="text-sm font-medium">Phone</label>
          <input
            type="text"
            disabled={!editMode}
            value={form.phone}
            onChange={(e) => handleChange("phone", e.target.value)}
            className={`mt-1 w-full px-3 py-2 rounded-lg border ${
              editMode ? "bg-white" : "bg-gray-100"
            }`}
          />
        </div>

      </div>

      {/* ======================
          BUSINESS INFO
      ====================== */}
      <h3 className="text-lg font-medium mb-3">Business Information</h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
        <div>
          <label className="text-sm font-medium">Business Name</label>
          <input
            disabled={!editMode}
            value={form.businessName}
            onChange={(e) => handleChange("businessName", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          />
        </div>

        <div>
          <label className="text-sm font-medium">Entity Type</label>
          <select
            disabled={!editMode}
            value={form.entityType}
            onChange={(e) => handleChange("entityType", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          >
            <option value="">Select</option>
            {ENTITY_TYPES.map((t) => (
              <option key={t}>{t}</option>
            ))}
          </select>
        </div>

        <div>
          <label className="text-sm font-medium">GST Number</label>
          <input
            disabled={!editMode}
            value={form.gstNumber}
            onChange={(e) => handleChange("gstNumber", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          />
        </div>

        <div>
          <label className="text-sm font-medium">PAN Number</label>
          <input
            disabled={!editMode}
            value={form.panNumber}
            onChange={(e) => handleChange("panNumber", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          />
        </div>
      </div>

      {/* ======================
          ADDRESS
      ====================== */}

      <h3 className="text-lg font-medium mb-3">Business Address</h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
        <div className="md:col-span-2">
          <label className="text-sm font-medium">Address Line 1</label>
          <input
            disabled={!editMode}
            value={form.addressLine1}
            onChange={(e) => handleChange("addressLine1", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          />
        </div>

        <div className="md:col-span-2">
          <label className="text-sm font-medium">Address Line 2</label>
          <input
            disabled={!editMode}
            value={form.addressLine2}
            onChange={(e) => handleChange("addressLine2", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          />
        </div>

        <div>
          <label className="text-sm font-medium">City</label>
          <select
            disabled={!editMode}
            value={form.city}
            onChange={(e) => handleChange("city", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          >
            <option value="">Select city</option>
            {CITIES.map((c) => (
              <option key={c}>{c}</option>
            ))}
          </select>
        </div>

        <div>
          <label className="text-sm font-medium">State</label>
          <select
            disabled={!editMode}
            value={form.state}
            onChange={(e) => handleChange("state", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          >
            <option value="">Select state</option>
            {STATES.map((s) => (
              <option key={s}>{s}</option>
            ))}
          </select>
        </div>

        <div>
          <label className="text-sm font-medium">Pincode</label>
          <input
            disabled={!editMode}
            value={form.pincode}
            onChange={(e) => handleChange("pincode", e.target.value)}
            className="mt-1 w-full px-3 py-2 rounded-lg border"
          />
        </div>
      </div>

      {/* ======================
          SELLER CATEGORIES
      ====================== */}

      <h3 className="text-lg font-medium mb-3">Business Categories</h3>

      <div className="grid grid-cols-2 md:grid-cols-3 gap-2 mb-8">
        {SELLER_CATEGORIES.map((cat) => {
          const checked = form.sellerCategories.includes(cat);
          return (
            <label key={cat} className="flex items-center gap-2 text-sm">
              <input
                type="checkbox"
                disabled={!editMode}
                checked={checked}
                onChange={() => toggleCategory(cat)}
              />
              {cat}
            </label>
          );
        })}
      </div>

      {/* ======================
          DOCUMENT UPLOADS
      ====================== */}
      <h3 className="text-lg font-medium mb-3">Documents</h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
        <div>
          <label className="text-sm font-medium">Business Registration</label>
          <input
            disabled={!editMode}
            type="file"
            onChange={(e) => handleFile("businessRegDoc", e.target.files?.[0] || null)}
            className="mt-1 w-full"
          />
        </div>

        <div>
          <label className="text-sm font-medium">GST Certificate</label>
          <input
            disabled={!editMode}
            type="file"
            onChange={(e) => handleFile("gstCertificate", e.target.files?.[0] || null)}
            className="mt-1 w-full"
          />
        </div>
      </div>

      {/* ======================
          EDIT / SAVE BUTTONS
      ====================== */}
      <div className="mt-6 flex justify-end gap-3">
        {!editMode ? (
          <button
            onClick={() => setEditMode(true)}
            className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
          >
            Edit Profile
          </button>
        ) : (
          <>
            <button
              onClick={() => setEditMode(false)}
              className="px-6 py-2 bg-gray-200 rounded-lg hover:bg-gray-300"
            >
              Cancel
            </button>

            <button
              onClick={handleSave}
              disabled={saving}
              className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50"
            >
              {saving ? "Saving..." : "Save Changes"}
            </button>
          </>
        )}
      </div>
    </div>
  );
}
