import React, { useState } from "react";

type Props = {
  images: string[];
};

const fallbackImg = "/images/default_product.png"; 
export default function ProductGallery({ images }: Props) {
  // Remove duplicates + empty images
  const safeImages = Array.from(
    new Set(images.filter((img) => img && img.trim() !== ""))
  );

  const galleryImages = safeImages.length > 0 ? safeImages : [fallbackImg];

  const [selectedIndex, setSelectedIndex] = useState(0);

  return (
    <div className="flex flex-col gap-3 w-full">
      {/* MAIN IMAGE */}
      <div className="w-full h-80 border rounded-lg bg-white shadow-sm flex items-center justify-center overflow-hidden">
        <img
          src={galleryImages[selectedIndex]}
          onError={(e) => (e.currentTarget.src = fallbackImg)}
          alt="Product"
          className="object-contain h-full"
        />
      </div>

      {/* THUMBNAILS */}
      <div className="flex gap-2 overflow-x-auto p-1">
        {galleryImages.map((img, i) => (
          <button
            key={i}
            onClick={() => setSelectedIndex(i)}
            className={`border rounded w-16 h-16 flex-shrink-0 p-1 transition 
              ${
                selectedIndex === i
                  ? "border-blue-600 shadow-md"
                  : "border-gray-300 hover:border-gray-500"
              }`}
          >
            <img
              src={img}
              onError={(e) => (e.currentTarget.src = fallbackImg)}
              className="object-contain w-full h-full"
            />
          </button>
        ))}
      </div>
    </div>
  );
}
