// ===========================================================
// WISHLIST
// ===========================================================
export async function getWishlist(token: string) {
  return authFetch("/api/wishlist/", token);
}

export async function toggleWishlist(
  token: string,
  productId: string,
  isInWishlist?: boolean
) {
  if (isInWishlist) {
    // Remove from wishlist  DELETE has no body
    return authFetch(`/api/wishlist/remove/${productId}/`, token, {
      method: "DELETE",
    });
  }

  // Add to wishlist  must send { product: productId }
  return authFetch(`/api/wishlist/add/`, token, {
    method: "POST",
    body: JSON.stringify({ product: productId }),
  });
}



