// ===========================================================
// QUOTATIONS
// ===========================================================
export async function getQuotationRequests(token: string) {
  return authFetch("/api/quotation-requests/", token);
}

export async function sendQuotationRequest(
  token: string,
  productId: number,
  variantId?: number | null,
  quantity: number = 1
) {
  const body: any = { quantity };   //  include quantity

  if (variantId) body.variant_id = variantId;  //  include variant_id if exists

  const res = await fetch(
    `${API_BASE}/api/products/${productId}/request-preproduct-quotation/`,
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(body),
    }
  );

  if (!res.ok) {
    throw new Error("Failed to request quotation");
  }

  return await res.json();
}



export async function uploadQuotationFile(token: string, requestId: number, file: File) {
  const formData = new FormData();
  formData.append("file", file);
  formData.append("request", requestId.toString());

  const res = await fetch(`${API_BASE}/api/quotation-requests/${requestId}/upload/`, {
    method: "POST",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });

  if (!res.ok) {
    const text = await res.text();
    throw new Error(text || "Failed to upload quotation");
  }
  return res.json();
}

