import React, { useState } from "react";
import { uploadProductsExcel } from "../../api/api";

type Props = {
  token: string;
  onClose: () => void;
  onSuccess: () => void;
};

type RowError = {
  row: number;
  error: string;
};

const UploadProduct: React.FC<Props> = ({
  token,
  onClose,
  onSuccess,
}) => {
  const [file, setFile] = useState<File | null>(null);
  const [uploading, setUploading] = useState(false);

  const [successCount, setSuccessCount] = useState<number | null>(null);
  const [rowErrors, setRowErrors] = useState<RowError[]>([]);
  const [generalError, setGeneralError] = useState<string | null>(null);

  /* ================= FILE ================= */
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFile(e.target.files?.[0] || null);
    setSuccessCount(null);
    setRowErrors([]);
    setGeneralError(null);
  };

  /* ================= SUBMIT ================= */
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!file) {
      setGeneralError("Please select an Excel file.");
      return;
    }

    setUploading(true);
    setSuccessCount(null);
    setRowErrors([]);
    setGeneralError(null);

    try {
      const res = await uploadProductsExcel(token, file);

      /*
        Expected backend response:
        {
          created: number,
          errors: [{ row: number, error: string }]
        }
      */

      setSuccessCount(res.created ?? 0);
      setRowErrors(res.errors ?? []);

      if (!res.errors || res.errors.length === 0) {
        onSuccess();
      }
    } catch (err: any) {
      setGeneralError(
        err?.message || "Failed to upload products. Please try again."
      );
    } finally {
      setUploading(false);
    }
  };

  /* ================= UI ================= */
  return (
    <div className="w-full min-h-screen flex items-center justify-center bg-gray-100 px-4 py-10">
      <div className="bg-white w-full max-w-2xl rounded-xl shadow-xl p-6">

        {/* HEADER */}
        <h2 className="text-2xl font-semibold text-gray-800 mb-4 text-center">
           Upload Products (Excel)
        </h2>

        {/* FORM */}
        <form onSubmit={handleSubmit} className="space-y-5">
          {/* FILE INPUT */}
          <label className="flex flex-col items-center justify-center border-2 border-dashed border-gray-300 
                            rounded-lg p-6 hover:bg-gray-50 cursor-pointer">
            <input
              type="file"
              accept=".xlsx,.xls"
              className="hidden"
              disabled={uploading}
              onChange={handleFileChange}
            />
            <span className="text-sm text-gray-600">
              Click to choose Excel file
            </span>

            {file && (
              <p className="mt-2 text-blue-600 text-sm font-medium">
                 {file.name}
              </p>
            )}
          </label>

          {/* ACTIONS */}
          <div className="flex gap-3">
            <button
              type="submit"
              disabled={!file || uploading}
              className={`flex-1 py-2 rounded-lg text-white font-medium ${
                uploading || !file
                  ? "bg-blue-300 cursor-not-allowed"
                  : "bg-blue-600 hover:bg-blue-700"
              }`}
            >
              {uploading ? "Uploading..." : "Upload"}
            </button>

            <button
              type="button"
              onClick={onClose}
              className="flex-1 py-2 rounded-lg bg-gray-200 hover:bg-gray-300 
                         text-gray-800 font-medium"
            >
              Cancel
            </button>
          </div>
        </form>

        {/* ================= RESULTS ================= */}

        {/* GENERAL ERROR */}
        {generalError && (
          <p className="mt-4 text-center text-red-600 font-medium">
             {generalError}
          </p>
        )}

        {/* SUCCESS MESSAGE */}
        {successCount !== null && rowErrors.length === 0 && (
          <p className="mt-4 text-center text-green-600 font-semibold">
             Successfully added all rows ({successCount} variants).
          </p>
        )}

        {/* PARTIAL SUCCESS */}
        {successCount !== null && rowErrors.length > 0 && (
          <div className="mt-6">
            <p className="text-green-600 font-medium mb-2">
               Successfully added {successCount} rows
            </p>

            <p className="text-red-600 font-medium mb-2">
               Errors in the following rows:
            </p>

            <div className="max-h-56 overflow-y-auto border rounded-lg">
              <table className="w-full text-sm">
                <thead className="bg-gray-100 sticky top-0">
                  <tr>
                    <th className="px-3 py-2 text-left">Row</th>
                    <th className="px-3 py-2 text-left">Error</th>
                  </tr>
                </thead>
                <tbody>
                  {rowErrors.map((err, i) => (
                    <tr key={i} className="border-t">
                      <td className="px-3 py-2 font-medium">
                        {err.row}
                      </td>
                      <td className="px-3 py-2 text-red-600">
                        {err.error}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {/* ================= INSTRUCTIONS ================= */}
        <div className="mt-6 border-t pt-4">
          <p className="text-sm font-semibold text-gray-700 mb-1">
            Expected Excel Columns
          </p>

          <div className="bg-gray-100 p-3 rounded-lg text-sm text-gray-700">
            name, ref_no, description, category, hsn, brand, cas_no, gst,
            variant_label, est_price, price, variant_discount
          </div>

          <p className="text-sm text-gray-600 mt-2">
            Use the same <strong>ref_no</strong> for multiple rows to
            create variants. Each row represents one variant with its
            own price and discount.
          </p>


          {/* TEMPLATE */}
          <div className="mt-4 flex justify-center">
            <a
              href="/files/Linkzur_products_upload.xlsx"
              download
              className="px-4 py-2 text-sm bg-green-600 hover:bg-green-700 
                         text-white rounded-lg font-medium"
            >
               Download Excel Template
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default UploadProduct;
