import React, { useEffect, useState } from "react";
import { X, ChevronDown } from "lucide-react";
import {
  getSellerOrders,
  updateOrderStatus,
  verifyDeliveryOTP,
  uploadInvoice,
} from "../../api/api";
import { Order, OrderStatus } from "../../types";

interface OrderPanelProps {
  token: string;
  onClose?: () => void;
}

const BACKEND_URL =
  process.env.NODE_ENV === "production" ? "" : "http://127.0.0.1:8000";

export default function OrderPanel({ token, onClose }: OrderPanelProps) {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(true);
  const [openMenu, setOpenMenu] = useState<number | null>(null);

  const [otpInputs, setOtpInputs] = useState<{ [key: number]: string }>({});

  //  NEW: Loading for "Deliver  OTP Sent"
  const [deliveryLoading, setDeliveryLoading] = useState<number | null>(null);

  // LOAD ORDERS
  const fetchOrders = async () => {
    try {
      const data = await getSellerOrders(token);
      setOrders(data);
    } catch (err) {
      console.error("Failed to fetch seller orders:", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOrders();
  }, []);

  // STATUS CHANGE + OTP SENT LOADING
  const handleStatusChange = async (orderId: number, newStatus: OrderStatus) => {
    try {
      // If marking delivered  show loading spinner
      if (newStatus === "delivered") {
        setDeliveryLoading(orderId);
      }

      const res = await updateOrderStatus(token, orderId, newStatus);

      if (res?.otp_sent) {
        alert("OTP sent to buyer for delivery verification.");
      }

      setOrders((prev) =>
        prev.map((o) =>
          o.id === orderId ? { ...o, status: newStatus } : o
        )
      );

      setOpenMenu(null);
    } catch (err) {
      alert("Failed to update order status");
      console.error(err);
    } finally {
      setDeliveryLoading(null); // stop spinner
    }
  };

  // OTP VERIFY
  const handleVerifyOTP = async (orderId: number) => {
    const otp = otpInputs[orderId];

    if (!otp || otp.length !== 6) {
      return alert("Please enter a valid 6-digit OTP");
    }

    try {
      await verifyDeliveryOTP(token, orderId, otp);

      alert("OTP verified successfully! Order marked as completed.");

      setOrders((prev) =>
        prev.map((o) =>
          o.id === orderId
            ? { ...o, status: "completed", is_delivered_verified: true }
            : o
        )
      );

      setOtpInputs((prev) => ({ ...prev, [orderId]: "" }));
    } catch (err) {
      alert("Invalid OTP");
      console.error(err);
    }
  };

  // UPLOAD INVOICE
  const handleUploadInvoice = async (orderId: number, file: File | null) => {
    if (!file) return alert("Select a PDF file");

    try {
      const res = await uploadInvoice(token, orderId, file);

      alert("Invoice uploaded successfully!");

      setOrders((prev) =>
        prev.map((o) =>
          o.id === orderId
            ? {
                ...o,
                invoice: {
                  ...o.invoice,
                  pdf_file: res.invoice_url,
                },
              }
            : o
        )
      );
    } catch (err) {
      alert("Failed to upload invoice");
      console.error(err);
    }
  };

  // SPLIT ADDRESS
  function splitAddress(full: string) {
    if (!full) return { shipping: "", billing: "" };
    const [ship, bill] = full.split(" | ");
    return {
      shipping: ship?.replace("Shipping:", "").trim() || "",
      billing: bill?.replace("Billing:", "").trim() || "",
    };
  }

  const statusColor = {
    pending: "bg-yellow-100 text-yellow-800 border-yellow-300",
    processing: "bg-purple-100 text-purple-800 border-purple-300",
    shipped: "bg-blue-100 text-blue-800 border-blue-300",
    delivered: "bg-green-100 text-green-800 border-green-300",
    completed: "bg-green-200 text-green-900 border-green-400",
    cancelled: "bg-red-100 text-red-800 border-red-300",
  };

  // LOADING SPINNER
  if (loading)
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin h-8 w-8 border-4 border-blue-500 border-t-transparent rounded-full" />
      </div>
    );

  return (
    <div className="p-6 max-w-5xl mx-auto">
      {/* HEADER */}
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-semibold">Orders</h2>

        {onClose && (
          <button
            onClick={onClose}
            className="p-2 rounded-full hover:bg-gray-200 transition"
          >
            <X size={24} />
          </button>
        )}
      </div>

      {orders.length === 0 ? (
        <p className="text-gray-500 text-center">No orders available.</p>
      ) : (
        <div className="space-y-6">
          {orders.map((order) => {
            const { shipping, billing } = splitAddress(order.address);

            return (
              <div
                key={order.id}
                className="bg-white border rounded-xl shadow-sm hover:shadow-md transition p-6"
              >
                {/* TOP */}
                <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-3">
                  <div>
                    <h3 className="text-xl font-bold">Order #{order.id}</h3>
                    <p className="text-sm text-gray-500">
                      {new Date(order.created_at).toLocaleString()}
                    </p>
                  </div>

                  {/* STATUS BUTTON */}
                  <div className="relative">
                    <button
                      disabled={deliveryLoading === order.id}
                      onClick={() =>
                        setOpenMenu(openMenu === order.id ? null : order.id)
                      }
                      className={`px-3 py-2 rounded-lg border flex items-center gap-2 text-sm 
                        ${statusColor[order.status]} 
                        ${
                          deliveryLoading === order.id
                            ? "opacity-50 cursor-not-allowed"
                            : ""
                        }`}
                    >
                      {deliveryLoading === order.id ? (
                        <div className="h-4 w-4 border-2 border-t-transparent border-black rounded-full animate-spin"></div>
                      ) : (
                        <>
                          {order.status.toUpperCase()}
                          <ChevronDown size={18} />
                        </>
                      )}
                    </button>

                    {/* DROPDOWN */}
                    {openMenu === order.id && deliveryLoading !== order.id && (
                      <div className="absolute right-0 mt-2 bg-white border shadow-lg rounded-md w-40 z-10">
                        {[
                          "pending",
                          "processing",
                          "shipped",
                          "delivered",
                          "cancelled",
                        ].map((status) => (
                          <button
                            key={status}
                            onClick={() =>
                              handleStatusChange(order.id, status as OrderStatus)
                            }
                            className="w-full px-3 py-2 text-left text-sm hover:bg-gray-100"
                          >
                            {status.toUpperCase()}
                          </button>
                        ))}
                      </div>
                    )}
                  </div>
                </div>

                {/* ADDRESSES */}
                <div className="grid md:grid-cols-2 gap-6 border-t pt-4 mt-4">
                  <div>
                    <h4 className="font-semibold mb-1">Shipping Address</h4>
                    <p className="text-sm text-gray-700">{shipping}</p>
                  </div>

                  <div>
                    <h4 className="font-semibold mb-1">Billing Address</h4>
                    <p className="text-sm text-gray-700">{billing}</p>
                  </div>
                </div>

                {/* ITEMS */}
                <div className="mt-6">
                  <h4 className="font-semibold mb-3">Items</h4>

                  <div className="space-y-4">
                    {order.items.map((item) => (
                      <div
                        key={item.id}
                        className="flex items-center justify-between bg-gray-50 border rounded-lg p-3"
                      >
                        <div className="flex items-center gap-3">
                          <img
                            src={
                              item.product.image
                                ? item.product.image.startsWith("http")
                                  ? item.product.image
                                  : `${BACKEND_URL}${item.product.image}`
                                : "/placeholder.png"
                            }
                            alt={item.product.name}
                            className="w-14 h-14 rounded object-cover bg-gray-200"
                            onError={(e) =>
                              ((e.target as HTMLImageElement).src =
                                "/placeholder.png")
                            }
                          />

                          <div>
                            <p className="font-medium">{item.product.name}</p>

                            {item.variant?.variant_label && (
                              <p className="text-xs text-gray-500">
                                Variant: {item.variant.variant_label}
                              </p>
                            )}
                          </div>
                        </div>

                        <div className="text-right">
                          <p className="text-sm">Qty: {item.quantity}</p>
                          <p className="font-semibold">{item.price}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>

                {/* OTP BOX */}
                {order.status === "delivered" &&
                  !order.is_delivered_verified && (
                    <div className="mt-4 bg-yellow-50 border border-yellow-300 p-4 rounded-lg">
                      <p className="text-sm font-medium text-yellow-800">
                        Enter the buyers OTP to complete delivery
                      </p>

                      <div className="flex items-center gap-3 mt-2">
                        <input
                          type="text"
                          maxLength={6}
                          value={otpInputs[order.id] || ""}
                          onChange={(e) =>
                            setOtpInputs((prev) => ({
                              ...prev,
                              [order.id]: e.target.value,
                            }))
                          }
                          placeholder="6-digit OTP"
                          className="border px-3 py-2 rounded w-40"
                        />

                        <button
                          onClick={() => handleVerifyOTP(order.id)}
                          className="px-4 py-2 bg-green-600 text-white rounded-lg"
                        >
                          Verify OTP
                        </button>
                      </div>
                    </div>
                  )}

                {/* INVOICE UPLOAD / DOWNLOAD */}
                <div className="mt-5 border-t pt-4">
                  <h4 className="font-semibold mb-2">Invoice</h4>

                  {order.invoice?.pdf_file ? (
                    <a
                      href={`${BACKEND_URL}${order.invoice.pdf_file}`}
                      target="_blank"
                      className="text-blue-600 underline text-sm"
                    >
                       Download Invoice
                    </a>
                  ) : (
                    <div className="flex items-center gap-2">
                      <input
                        type="file"
                        accept="application/pdf"
                        onChange={(e) =>
                          handleUploadInvoice(order.id, e.target.files?.[0] || null)
                        }
                        className="text-sm"
                      />
                    </div>
                  )}
                </div>

                {/* TOTAL */}
                <div className="mt-4 text-right text-lg font-bold">
                  Total: {order.total_price}
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}

