import React, { useState, useRef, useEffect, KeyboardEvent } from "react";
import { Search, Bell, User, ChevronDown, Menu } from "lucide-react";
import { useNavigate } from "react-router-dom";

type SellerHeaderProps = {
  token?: string | null;
  unreadCount?: number;
  onUserPress: () => void;
  onSearch: (query: string) => void;
  onOrdersPress: () => void;
  onChatPress: () => void;
  onQuotationsPress: () => void;
  onNotificationsPress: () => void;
  onAboutPress: () => void;
  onLogout: () => void;
  onDashboardPress: () => void;

};

export default function SellerHeader({
  token,
  unreadCount = 0,
  onUserPress,
  onSearch,
  onOrdersPress,
  onChatPress,
  onQuotationsPress,
  onNotificationsPress,
  onAboutPress,
  onLogout,
  onDashboardPress,
}: SellerHeaderProps) {
  const navigate = useNavigate();
  const dropdownRef = useRef<HTMLDivElement>(null);

  const [searchText, setSearchText] = useState("");
  const [showDropdown, setShowDropdown] = useState(false);
  const [mobileSearchOpen, setMobileSearchOpen] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  // Close dropdown when clicking outside
  useEffect(() => {
    const listener = (e: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(e.target as Node)
      ) {
        setShowDropdown(false);
      }
    };
    document.addEventListener("mousedown", listener);
    return () => document.removeEventListener("mousedown", listener);
  }, []);

  const handleKeyPress = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") onSearch(searchText.trim());
  };

  return (
    <>
      {/* ================= HEADER ================= */}
      <header className="w-full sticky top-0 z-50 bg-white shadow flex items-center justify-between px-4 lg:px-10 h-16">

        {/* LEFT SIDE */}
        <div className="flex items-center gap-3">
          <Menu
            className="w-6 h-6 text-gray-800 lg:hidden cursor-pointer"
            onClick={() => setSidebarOpen(true)}
          />
          <img
            src="/images/Linkzur_web.jpg"
            alt="logo"
            onClick={() => navigate("/seller/home")}
            className="h-full max-h-16 w-auto md:max-h-16 object-contain"
          />
        </div>

        {/* SEARCH BAR DESKTOP */}
        <div className="hidden lg:flex items-center gap-2 w-[40%]">
          <div className="flex items-center bg-gray-100 rounded-full px-4 py-2 w-full">
            <Search className="w-5 h-5 text-gray-500" />
            <input
              placeholder="Search products..."
              className="bg-transparent w-full ml-2 focus:outline-none"
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
              onKeyDown={handleKeyPress}
            />
          </div>

          <button
            onClick={() => onSearch(searchText.trim())}
            className="bg-blue-600 text-white px-5 py-2 rounded-full hover:bg-blue-700"
          >
            Search
          </button>
        </div>

        {/* RIGHT SIDE */}
        <div className="flex items-center gap-4">

          {/* Mobile Search Toggle */}
          <button
            className="lg:hidden text-gray-700"
            onClick={() => setMobileSearchOpen(!mobileSearchOpen)}
          >
            <Search className="w-6 h-6" />
          </button>

          {/* Notifications */}
          {token && (
            <button
              onClick={onNotificationsPress}
              className="relative hidden lg:block text-gray-700"
            >
              <Bell className="w-6 h-6" />
              {unreadCount > 0 && (
                <span className="absolute -top-1 -right-2 bg-red-600 text-white text-[10px] px-1.5 py-0.5 rounded-full">
                  {unreadCount}
                </span>
              )}
            </button>
          )}

          {/* LOGIN / DROPDOWN */}
          {!token ? (
            <div className="hidden lg:flex gap-4 items-center">
              <button onClick={() => navigate("/login")} className="font-semibold">
                Login
              </button>
              <button
                onClick={() => navigate("/register")}
                className="bg-blue-600 text-white px-4 py-2 rounded-full hover:bg-blue-700"
              >
                Sign Up
              </button>
            </div>
          ) : (
            <div className="relative hidden lg:block" ref={dropdownRef}>
              <button
                onClick={() => setShowDropdown(!showDropdown)}
                className="flex items-center gap-1 text-gray-800"
              >
                <User className="w-6 h-6" />
                <ChevronDown className="w-4 h-4" />
              </button>

              {showDropdown && (
                <div className="absolute right-0 mt-2 bg-white shadow-xl rounded-lg w-48 py-2 border">
                  <button onClick={onUserPress} className="block w-full text-left px-4 py-2 text-sm hover:bg-gray-100">
                    My Profile
                  </button>
                  <button
                    onClick={onDashboardPress}
                    className="block w-full text-left px-4 py-2 text-sm hover:bg-gray-100"
                  >
                    Dashboard
                  </button>

                  <button onClick={onOrdersPress} className="block w-full text-left px-4 py-2 text-sm hover:bg-gray-100">
                    Orders
                  </button>
                  <button onClick={onChatPress} className="block w-full text-left px-4 py-2 text-sm hover:bg-gray-100">
                    Chat
                  </button>
                  <button onClick={onQuotationsPress} className="block w-full text-left px-4 py-2 text-sm hover:bg-gray-100">
                    Quotations
                  </button>
                 
                  <button onClick={onAboutPress} className="block w-full text-left px-4 py-2 text-sm hover:bg-gray-100">
                    About Us
                  </button>

                  <hr className="my-2" />

                  <button
                    onClick={onLogout}
                    className="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50"
                  >
                    Logout
                  </button>
                </div>
              )}
            </div>
          )}
        </div>
      </header>

      {/* ================= MOBILE SEARCH ================= */}
      {mobileSearchOpen && (
        <div className="lg:hidden w-full bg-white px-4 py-3 border-b">
          <div className="flex items-center bg-gray-100 rounded-full px-4 py-2">
            <Search className="w-5 h-5 text-gray-500" />
            <input
              placeholder="Search..."
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
              onKeyDown={handleKeyPress}
              className="bg-transparent ml-2 w-full focus:outline-none"
            />
            {/*  Mobile Search Button */}
          <button
            onClick={() => onSearch(searchText.trim())}
            className="ml-2 text-blue-600 font-semibold"
          >
            Go
          </button>
          </div>
        </div>
      )}

      {/* ================= MOBILE SIDEBAR ================= */}
      {sidebarOpen && (
        <>
          <div
            className="fixed inset-0 bg-black/40 z-40"
            onClick={() => setSidebarOpen(false)}
          />

          <div className="fixed top-0 left-0 h-full w-72 bg-white shadow-xl z-50 p-5 animate-slideIn">
            <div className="flex justify-between items-center mb-5">
              <h3 className="text-lg font-semibold">Menu</h3>
              <button onClick={() => setSidebarOpen(false)}></button>
            </div>

            {!token ? (
              <>
                <button className="sidebar-btn" onClick={() => navigate("/login")}>
                  Login
                </button>
                <button className="sidebar-btn" onClick={() => navigate("/register")}>
                  Sign Up
                </button>
              </>
            ) : (
              <>
                <button className="sidebar-btn" onClick={onUserPress}>My Profile</button>
                <button className="sidebar-btn" onClick={onDashboardPress}>Dashboard</button>
                <button className="sidebar-btn" onClick={onOrdersPress}>Orders</button>
                <button className="sidebar-btn" onClick={onChatPress}>Chat</button>
                <button className="sidebar-btn" onClick={onQuotationsPress}>Quotations</button>
                <button className="sidebar-btn" onClick={onNotificationsPress}>Notifications</button>
                <button className="sidebar-btn" onClick={onAboutPress}>About Us</button>
                <button className="sidebar-btn text-red-600" onClick={onLogout}>Logout</button>
              </>
            )}
          </div>

          <style>
            {`
            @keyframes slideIn {
              from { transform: translateX(-100%); }
              to { transform: translateX(0); }
            }
            .animate-slideIn {
              animation: slideIn 0.3s ease-out;
            }
            .sidebar-btn {
              width: 100%;
              padding: 12px 0;
              border-bottom: 1px solid #eee;
              text-align: left;
              font-size: 15px;
            }
          `}
          </style>
        </>
      )}
    </>
  );
}
