import React, { useEffect, useState, useRef } from "react";
import { CartItem } from "../../types";
import { placeOrder, getProfile, updateProfile } from "../../api/api";

type Props = {
  token: string;
  cartItems: CartItem[];
  onClose: () => void;
  onOrderPlaced?: () => void;
};

type OrderResponse = { id: string };

type ShippingAddress = {
  id?: number;
  name: string;
  phone: string;
  addressLine1: string;
  addressLine2?: string;
  city: string;
  state: string;
  pincode: string;
  isDefault: boolean;
};

type BillingAddress = {
  name: string;
  phone: string;
  addressLine1: string;
  addressLine2?: string;
  city: string;
  state: string;
  pincode: string;
};

/* ======================================================
   PRICE HELPER (VARIANT DISCOUNT + GST)
====================================================== */
const calculateFinalPrice = (item: CartItem) => {
  const variant = item.variant ?? null;

  const basePrice = Number(
    variant?.price ?? variant?.est_price ?? 0
  );

  const discount = Number(variant?.discount ?? 0);
  const gst = Number(item.product.gst ?? 0);

  // 1 Apply discount
  const discountedPrice =
    basePrice - (basePrice * discount) / 100;

  // 2 Apply GST on discounted price
  const gstAmount = (discountedPrice * gst) / 100;

  // 3 Final unit price (GST included)
  return discountedPrice + gstAmount;
};

export default function PaymentPanel({
  token,
  cartItems,
  onClose,
  onOrderPlaced,
}: Props) {
  const scrollRef = useRef<HTMLDivElement>(null);
  const [loading, setLoading] = useState(false);

  /* ---------------- PROFILE ADDRESSES ---------------- */
  const [shippingList, setShippingList] = useState<ShippingAddress[]>([]);
  const [billing, setBilling] = useState<BillingAddress | null>(null);
  const [selectedShippingId, setSelectedShippingId] =
    useState<number | null>(null);

  const [addingShipping, setAddingShipping] = useState(false);
  const [addingBilling, setAddingBilling] = useState(false);

  const [shippingForm, setShippingForm] = useState<ShippingAddress>({
    name: "",
    phone: "",
    addressLine1: "",
    addressLine2: "",
    city: "",
    state: "",
    pincode: "",
    isDefault: false,
  });

  const [billingForm, setBillingForm] = useState<BillingAddress>({
    name: "",
    phone: "",
    addressLine1: "",
    addressLine2: "",
    city: "",
    state: "",
    pincode: "",
  });

  /* ---------------- LOAD PROFILE ---------------- */
  useEffect(() => {
    async function load() {
      const profile = await getProfile(token);

      setShippingList(profile.shippingAddresses || []);
      setBilling(profile.billingAddress || null);

      const defaultShip = (profile.shippingAddresses || []).find(
        (a: ShippingAddress) => a.isDefault
      );
      setSelectedShippingId(defaultShip?.id || null);
    }
    load();
  }, [token]);

  /* ---------------- AUTO SCROLL ---------------- */
  useEffect(() => {
    if (addingShipping || addingBilling) {
      scrollRef.current?.scrollTo({ top: 0, behavior: "smooth" });
    }
  }, [addingShipping, addingBilling]);

  /* ---------------- TOTAL ---------------- */
  const total = cartItems.reduce((sum, item) => {
    return sum + calculateFinalPrice(item) * item.quantity;
  }, 0);

  /* ---------------- SAVE SHIPPING ---------------- */
  const saveNewShipping = async () => {
    if (!shippingForm.name || !shippingForm.addressLine1 || !shippingForm.city)
      return alert("Please fill all required fields");

    const updated = [
      ...shippingList,
      { ...shippingForm, id: Date.now() },
    ];

    await updateProfile(token, {
      shippingAddresses: updated,
      billingAddress: billing ?? billingForm,
    });

    setShippingList(updated);
    setSelectedShippingId(updated[updated.length - 1].id!);
    setAddingShipping(false);
  };

  /* ---------------- SAVE BILLING ---------------- */
  const saveBilling = async () => {
    if (!billingForm.name || !billingForm.addressLine1 || !billingForm.city)
      return alert("Please complete billing address");

    await updateProfile(token, {
      shippingAddresses: shippingList,
      billingAddress: billingForm,
    });

    setBilling(billingForm);
    setAddingBilling(false);
  };

  /* ---------------- PLACE ORDER ---------------- */
  const handleInvoice = async () => {
    if (!selectedShippingId && shippingList.length > 0)
      return alert("Please select a shipping address");

    if (!billing && !billingForm.addressLine1)
      return alert("Please add a billing address");

    let shippingAddressStr = "";
    let billingAddressStr = "";

    const selected = shippingList.find((s) => s.id === selectedShippingId);

    shippingAddressStr = selected
      ? `${selected.name}, ${selected.addressLine1}, ${selected.city}, ${selected.state} - ${selected.pincode}`
      : `${shippingForm.name}, ${shippingForm.addressLine1}, ${shippingForm.city}, ${shippingForm.state} - ${shippingForm.pincode}`;

    billingAddressStr = billing
      ? `${billing.name}, ${billing.addressLine1}, ${billing.city}, ${billing.state} - ${billing.pincode}`
      : `${billingForm.name}, ${billingForm.addressLine1}, ${billingForm.city}, ${billingForm.state} - ${billingForm.pincode}`;

    const finalAddress = `Shipping: ${shippingAddressStr} | Billing: ${billingAddressStr}`;

    setLoading(true);

    try {
      const items = cartItems.map((ci) => ({
        product_id: String(ci.product.id),
        variant_id: ci.variant ? String(ci.variant.id) : undefined,
        quantity: ci.quantity,
        price: calculateFinalPrice(ci), // GST-inclusive unit price
      }));

      await placeOrder(token, items, finalAddress);

      alert("Order placed successfully!");
      onOrderPlaced?.();
      onClose();
    } catch (err) {
      console.error(err);
      alert("Failed to place order.");
    } finally {
      setLoading(false);
    }
  };

  /* ======================================================
     UI (unchanged)
  ====================================================== */

  return (
    <div className="fixed inset-0 bg-black/40 flex items-end justify-center z-50">
      <div className="w-full max-w-lg bg-white rounded-t-2xl shadow-xl flex flex-col h-[90vh]">
        {/* HEADER */}
        <div className="px-6 py-4 border-b bg-gray-50 flex justify-between items-center sticky top-0 z-10">
          <h2 className="text-lg font-semibold">Checkout</h2>
          <button onClick={onClose} className="text-xl"></button>
        </div>

        {/* CONTENT */}
        <div ref={scrollRef} className="flex-1 overflow-y-auto px-6 py-4 space-y-6">
          {/* ORDER SUMMARY */}
          <div>
            <h3 className="font-semibold mb-2">Order Summary</h3>
            {cartItems.map((item) => {
              const price = calculateFinalPrice(item);
              return (
                <div key={item.id || item.product.id} className="flex justify-between border-b pb-2">
                  <span>{item.product.name}</span>
                  <span>
                    {price.toFixed(2)}  {item.quantity}
                  </span>
                </div>
              );
            })}
          </div>

          <p className="text-right text-lg font-semibold">
            Total: {total.toFixed(2)}
          </p>
        </div>

        {/* FOOTER */}
        <div className="px-6 py-4 border-t flex justify-end gap-3">
          <button onClick={onClose} className="px-4 py-2 border rounded">
            Cancel
          </button>
          <button
            onClick={handleInvoice}
            disabled={loading}
            className="px-5 py-2 bg-blue-600 text-white rounded"
          >
            {loading ? "Processing" : "Place Order"}
          </button>
        </div>
      </div>
    </div>
  );
}

