import React, { useState } from "react";
import { X } from "lucide-react";
import { uploadQuotationFile } from "../../api/api";
import { QuotationRequest } from "../../types";

interface QuotationPanelProps {
  token: string;
  requests: QuotationRequest[];
  onClose?: () => void; // <-- ADDED CLOSE SUPPORT
}

export default function QuotationPanel({
  token,
  requests,
  onClose,
}: QuotationPanelProps) {
  const [loading, setLoading] = useState(false);
  const [activeRequestId, setActiveRequestId] = useState<number | null>(null);

  const handleUpload = async (requestId: number, file: File) => {
    setLoading(true);
    setActiveRequestId(requestId);

    try {
      await uploadQuotationFile(token, requestId, file);
      alert("Quotation uploaded successfully!");
      window.location.reload();
    } catch (err: any) {
      console.error("Upload error:", err);
      alert(`Failed to upload: ${err.message || "Unknown error"}`);
    } finally {
      setLoading(false);
      setActiveRequestId(null);
    }
  };

  const handleDownload = (fileUrl: string) => {
    if (!fileUrl) return alert("No file available.");
    window.open(fileUrl, "_blank");
  };

  // -----------------------------
  // EMPTY STATE
  // -----------------------------
  if (!requests || requests.length === 0) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-100 p-6">
        <div className="text-center bg-white shadow-md rounded-xl p-10 w-full max-w-md">

          {/* Close button */}
          {onClose && (
            <div className="flex justify-end mb-3">
              <button
                onClick={onClose}
                className="p-2 rounded-full hover:bg-gray-200 transition"
              >
                <X size={22} className="text-gray-700" />
              </button>
            </div>
          )}

          <div className="text-5xl mb-4"></div>
          <h3 className="text-lg font-semibold text-gray-700">
            No quotation requests yet
          </h3>
          <p className="text-sm text-gray-500 mt-1">You're all synced!</p>
        </div>
      </div>
    );
  }

  // -----------------------------
  // MAIN PANEL UI
  // -----------------------------
  return (
    <div className="min-h-screen bg-gray-100 p-5 md:p-10">
      <div className="max-w-5xl mx-auto">

        {/* Header + Close Button */}
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-semibold text-gray-900">
            Quotation Requests
          </h2>

          {onClose && (
            <button
              onClick={onClose}
              className="p-2 rounded-full hover:bg-gray-200 transition"
            >
              <X size={24} className="text-gray-700" />
            </button>
          )}
        </div>

        {/* Quotation Items */}
        <div className="space-y-6">
          {requests.map((req) => {
            const quote = req.quotation;
            const isUploaded = !!quote?.file_url;

            return (
              <div
                key={req.id}
                className="bg-white shadow-md rounded-xl p-6 border border-gray-200"
              >
                {/* STATUS HEADER */}
                <div className="flex justify-between items-start">
                  <span
                    className={`px-3 py-1 text-xs rounded-full font-semibold ${
                      isUploaded
                        ? "bg-green-100 text-green-700 border border-green-300"
                        : "bg-yellow-100 text-yellow-700 border border-yellow-300"
                    }`}
                  >
                    {isUploaded ? "Uploaded" : "Pending"}
                  </span>

                  <span className="text-xs text-gray-500">
                    {new Date(req.created_at).toLocaleString()}
                  </span>
                </div>

                {/* INFO */}
                <div className="mt-4 grid md:grid-cols-2 gap-4">

                  {/* PRODUCT NAME */}
                  <div>
                    <p className="text-xs text-gray-500">Product</p>
                    <p className="font-medium text-gray-800">
                      {req.product.name}
                    </p>
                  </div>

                  {/* PRODUCT REF NO */}
                  <div>
                    <p className="text-xs text-gray-500">Ref No</p>
                    <p className="font-medium text-gray-800">
                      {req.product.ref_no || ""}
                    </p>
                  </div>

                  {/* VARIANT */}
                  <div>
                    <p className="text-xs text-gray-500">Variant</p>
                    <p className="font-medium text-gray-800">
                      {req.variant?.variant_label || "None"}
                    </p>
                  </div>

                  {/* BUYER EMAIL */}
                  <div>
                    <p className="text-xs text-gray-500">Buyer Email</p>
                    <p className="font-medium text-gray-800 break-all">
                      {req.buyer}
                    </p>
                  </div>

                </div>


                {/* ACTIONS */}
                <div className="mt-6 space-y-4">
                  {isUploaded && (
                    <>
                      {/* DOWNLOAD BUTTON */}
                      <button
                        className="w-full md:w-auto bg-blue-600 text-white px-5 py-2 rounded-lg hover:bg-blue-700 shadow-sm flex items-center justify-center gap-2"
                        onClick={() =>
                          quote?.file_url && handleDownload(quote.file_url)
                        }
                      >
                         Download PDF
                      </button>

                      {/* NOTE */}
                      {quote?.note && (
                        <p className="text-sm text-gray-700 bg-gray-50 border rounded-lg p-3">
                          <strong>Note:</strong> {quote.note}
                        </p>
                      )}
                    </>
                  )}

                  {/* UPLOAD / REPLACE */}
                  <label
                    className={`block w-full px-4 py-3 border rounded-lg cursor-pointer text-sm ${
                      loading && activeRequestId === req.id
                        ? "bg-gray-100 text-gray-400 border-gray-300"
                        : "bg-white border-gray-300 text-gray-700 hover:bg-gray-50"
                    }`}
                  >
                    {isUploaded
                      ? "Replace Quotation PDF"
                      : "Upload Quotation PDF"}

                    <input
                      type="file"
                      accept="application/pdf"
                      className="hidden"
                      disabled={loading && activeRequestId === req.id}
                      onChange={(e) => {
                        const file = e.target.files?.[0];
                        if (file) {
                          handleUpload(req.id, file);
                          e.target.value = "";
                        }
                      }}
                    />
                  </label>

                  {/* Upload progress bar */}
                  {loading && activeRequestId === req.id && (
                    <div className="w-full bg-gray-200 rounded-full h-2 overflow-hidden">
                      <div
                        className="bg-blue-600 h-full animate-pulse"
                        style={{ width: "70%" }}
                      ></div>
                    </div>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
