import React, { useEffect, useState } from "react";
import { X, MessageCircle } from "lucide-react";
import { getOrders, startOrGetConversation } from "../../api/api";
import { Order } from "../../types";


type Props = {
  token: string;
  onClose: () => void;
  onStartChat: (conversationId: number) => void;
};

const DEFAULT_IMAGE = "/images/default_product.png";

const BACKEND_URL =
  process.env.NODE_ENV === "production" ? "" : "http://127.0.0.1:8000";

export default function OrdersList({ token, onClose, onStartChat }: Props) {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState("All");

  // Load Orders
  useEffect(() => {
    const loadOrders = async () => {
      try {
        const data = await getOrders(token);
        setOrders(data || []);
      } catch {
        alert("Failed to load orders");
        onClose();
      } finally {
        setLoading(false);
      }
    };
    loadOrders();
  }, [token, onClose]);

  // Start chat with seller
  const handleChatWithSeller = async (productId: string, orderId: number) => {
    try {
      const data = await startOrGetConversation(token, productId, orderId);
      onStartChat(data.id);
    } catch {
      alert("Unable to start chat with seller.");
    }
  };

  // Order Status Colors
  const getStatusStyle = (status: string) => {
    const s = status.toLowerCase();
    switch (s) {
      case "pending":
        return { bg: "bg-yellow-100", border: "border-yellow-400", text: "text-yellow-700" };
      case "processing":
        return { bg: "bg-blue-100", border: "border-blue-400", text: "text-blue-700" };
      case "shipped":
        return { bg: "bg-cyan-100", border: "border-cyan-400", text: "text-cyan-700" };
      case "delivered":
        return { bg: "bg-green-100", border: "border-green-400", text: "text-green-700" };
      case "cancelled":
        return { bg: "bg-red-100", border: "border-red-400", text: "text-red-700" };
      default:
        return { bg: "bg-gray-100", border: "border-gray-400", text: "text-gray-700" };
    }
  };

  // Filter orders
  const filteredOrders =
    filter === "All"
      ? orders
      : orders.filter((o) => o.status.toLowerCase().includes(filter.toLowerCase()));

  if (loading)
    return (
      <div className="flex flex-col items-center justify-center h-full py-20">
        <div className="w-10 h-10 border-4 border-gray-300 border-t-blue-600 rounded-full animate-spin"></div>
        <p className="mt-4 text-gray-600">Loading your orders...</p>
      </div>
    );

  return (
    <div className="w-full max-w-5xl mx-auto p-6 pb-10">
      {/* HEADER */}
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-bold">My Purchases</h1>

        <button
          onClick={onClose}
          className="p-2 rounded-full hover:bg-gray-200 transition"
        >
          <X size={22} />
        </button>
      </div>

      {/* FILTERS */}
      <div className="flex gap-2 mb-6 overflow-x-auto pb-2">
        {["All", "Pending", "Processing", "Shipped", "Delivered", "Cancelled"].map(
          (f) => (
            <button
              key={f}
              className={`px-4 py-2 rounded-full text-sm font-medium border transition ${
                filter === f
                  ? "bg-blue-600 text-white border-blue-600"
                  : "bg-white border-gray-300 text-gray-700 hover:bg-gray-100"
              }`}
              onClick={() => setFilter(f)}
            >
              {f}
            </button>
          )
        )}
      </div>

      {/* NO ORDERS */}
      {filteredOrders.length === 0 && (
        <div className="text-center py-20">
          <p className="text-6xl mb-3"></p>
          <p className="text-xl font-semibold text-gray-700">
            No orders found
          </p>
          <p className="text-gray-500 text-sm mt-1">You haven't placed any orders yet.</p>
        </div>
      )}

      {/* ORDER CARDS */}
      <div className="flex flex-col gap-6">
        {filteredOrders.map((order) => {
          const style = getStatusStyle(order.status);

          return (
            <div
              key={order.id}
              className="border border-gray-200 rounded-lg shadow-sm bg-white"
            >
              {/* ORDER STATUS BAR */}
              <div
                className={`px-4 py-3 border-b flex items-center justify-between ${style.bg} ${style.border}`}
              >
                <span className={`font-semibold ${style.text} text-lg`}>
                  {order.status.toUpperCase()}
                </span>
                <span className="text-sm text-gray-600">
                  Order ID: #{order.id}
                </span>
              </div>

              {/* ORDER ITEMS */}
              {order.items.map((item) => {
                const price =
                  Number(item.price) ||
                  Number(item.variant?.price) ||
                  Number(item.variant?.est_price) ||
                  0;

                return (
                  <div
                    key={item.id}
                    className="flex gap-4 p-4 border-b last:border-0"
                  >
                    {/* IMAGE */}
                    <img
                      src={
                        item.product.image
                          ? item.product.image.startsWith("http")
                            ? item.product.image
                            : `${BACKEND_URL}${item.product.image}`
                          : DEFAULT_IMAGE
                      }
                      onError={(e) =>
                        ((e.target as HTMLImageElement).src = DEFAULT_IMAGE)
                      }
                      className="w-28 h-28 object-cover rounded-md border"
                    />

                    {/* INFO */}
                    <div className="flex flex-col justify-between flex-1">
                      <div>
                        <p className="text-gray-600 text-sm">
                          Est. delivery:{" "}
                          {new Date(
                            Date.now() + 2 * 86400000
                          ).toLocaleDateString()}
                        </p>

                        <p className="font-semibold text-lg">
                          {item.product.name}
                        </p>

                        <p className="text-sm ">
                          {item.variant?.variant_label || "None"}
                        </p>

                        <p className="text-sm">
                          Ref No: {item.product.ref_no}
                        </p>

                        {order.invoice?.pdf_file && (
                            <a
                              href={`${BACKEND_URL}${order.invoice.pdf_file}`}
                              target="_blank"
                              className="text-blue-600 underline text-sm block mt-2"
                            >
                               Download Invoice
                            </a>
                          )}


                        <p className="text-gray-700 mt-1">
                           {price.toFixed(2)}  {item.quantity} ={" "}
                          <span className="font-semibold">
                            {(price * item.quantity).toFixed(2)}
                          </span>
                        </p>
                      </div>

                      {/* ACTIONS */}
                      <button
                        onClick={() =>
                          handleChatWithSeller(
                            item.product.id.toString(),
                            order.id
                          )
                        }
                        className="mt-3 bg-blue-600 hover:bg-blue-700 text-white text-sm px-4 py-2 rounded-lg flex items-center gap-2 w-max"
                      >
                        <MessageCircle size={16} />
                        Contact Seller
                      </button>
                    </div>
                  </div>
                );
              })}
            </div>
          );
        })}
      </div>
    </div>
  );
}

