import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { loginUser, getProfile } from "../api/api";

export default function Login() {
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!email.trim() || !password.trim()) {
      setError("Email and Password are required");
      return;
    }

    setLoading(true);
    setError("");

    try {
      // Step 1  login user and get JWT token
      const res = await loginUser(email, password);

      localStorage.setItem("access_token", res.access);
      localStorage.setItem("refresh_token", res.refresh);

      // Step 2  fetch user role
      const profile = await getProfile(res.access);
      const role = profile.role?.toLowerCase() || "";

      // Step 3  route based on role
      if (role === "buyer") {
        navigate("/buyer/home");
      } else if (role === "seller") {
        navigate("/seller/home");
      } else {
        setError("Invalid role in profile.");
      }
    } catch (err: any) {
      setError("Invalid email or password");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col">
      {/* Top Navigation (Same as Register Page) */}
      <header className="w-full border-b bg-white">
        <div className="max-w-5xl mx-auto flex items-center justify-between px-4 py-3">
          <div className="flex items-center gap-3">
            <img
              src="images/Linkzur_web.jpg"
              alt="Linkzur Logo"
              className="h-full max-h-12 md:max-h-16 w-auto object-contain"
            />
          </div>

          <div className="text-sm">
            <span className="text-gray-600 mr-2">Dont have an account?</span>
            <button
              onClick={() => navigate("/register")}
              className="inline-flex items-center rounded-full border border-blue-600 px-4 py-1.5 text-xs font-semibold text-blue-600 hover:bg-blue-50"
            >
              Register
            </button>
          </div>
        </div>
      </header>

      {/* Main Login Box */}
      <main className="flex-1 flex items-center justify-center px-4 py-10">
        <div className="w-full max-w-md bg-white border border-gray-200 shadow-md rounded-xl p-8">
          <h2 className="text-2xl font-semibold text-center text-gray-900 mb-2">
            Welcome Back
          </h2>
          <p className="text-center text-gray-500 mb-6 text-sm">
            Log in to your account
          </p>

          <form onSubmit={handleLogin} className="space-y-5">
            {/* Email */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Email Address
              </label>
              <input
                type="email"
                className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm 
                           focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder="example@mail.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>

            {/* Password */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Password
              </label>
              <div className="relative">
                <input
                  type={showPassword ? "text" : "password"}
                  className="w-full rounded-lg border border-gray-300 px-3 py-2 pr-10 text-sm 
                             focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  placeholder=""
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />

                <button
                  type="button"
                  onClick={() => setShowPassword((s) => !s)}
                  className="absolute inset-y-0 right-0 px-3 flex items-center text-xs text-gray-500"
                >
                  {showPassword ? "Hide" : "Show"}
                </button>
              </div>
            </div>

            {/* Error Message */}
            {error && (
              <div className="text-center text-red-500 text-sm mt-1">
                {error}
              </div>
            )}

            {/* Submit */}
            <button
              type="submit"
              disabled={loading}
              className="w-full rounded-lg bg-blue-600 text-white text-sm font-semibold py-3 
                         hover:bg-blue-700 transition disabled:opacity-60 disabled:cursor-not-allowed"
            >
              {loading ? "Logging in..." : "Log In"}
            </button>
          </form>

          {/* Forgot password */}
          <p className="mt-4 text-xs text-center text-gray-500">
            Forgot your password?{" "}
           <span
              className="text-blue-600 cursor-pointer hover:underline"
              onClick={() => navigate("/forgot-password")}
            >
              Reset here
            </span>

          </p>
        </div>
      </main>
    </div>
  );
}
