// src/pages/seller/SellerHome.tsx

import React, { useEffect, useState, JSX } from "react";
import { useNavigate } from "react-router-dom";

import Header from "../../components/seller/SellerHeader";
import BannerAd from "../../components/common/Banner";
import ProductsList from "../../components/common/ProductsList";
import UpdateProduct from "../../components/seller/UpdateProduct";
import AddProduct from "../../components/seller/AddProduct";
import UploadProduct from "../../components/seller/UploadProduct";
import Chat from "../../components/common/ChatPanel";
import QuotationPanel from "../../components/seller/QuotationPanel";
import OrderPanel from "../../components/seller/OrderPanel";
import SellerDashboard from "../../components/seller/SellerDashboard";
import SellerProfile from "../../components/seller/SellerProfile";
import About from "../../components/common/website/About";
import NotificationsPanel from "../../components/common/NotificationsPanel";
import DisplayProducts from "../../components/common/DisplayProducts";


import {
  Product,
  Notification,
  QuotationRequest,
} from "../../types";

import {
  getProducts,
  getNotifications,
  getQuotationRequests,
  markNotificationRead,
} from "../../api/api";

export default function SellerHome() {
  const navigate = useNavigate();

  // STATE -----------------------------------------------------
  const [token, setToken] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);
  const [allProducts, setAllProducts] = useState<Product[]>([]);


  const [products, setProducts] = useState<Product[]>([]);
  const [searchResults, setSearchResults] = useState<Product[]>([]);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);

  const [searching, setSearching] = useState(false);

  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [quotationRequests, setQuotationRequests] = useState<QuotationRequest[]>([]);

  const [activePanel, setActivePanel] = useState<
    | "dashboard"
    | "products"
    | "updateProduct"
    | "addProduct"
    | "uploadProduct"
    | "chat"
    | "orders"
    | "quotations"
    | "notifications"
    | "profile"
    | "about"
    | "products_all"

  >("products");

  // CONSTANTS -----------------------------------------------------
  const BACKEND_URL =
    process.env.NODE_ENV === "production"
      ? "https://linkzur-backend.onrender.com"
      : "http://127.0.0.1:8000";

  const DEFAULT_IMAGE = "https://via.placeholder.com/150";

  const normalizeImage = (img?: string | null) =>
    img
      ? img.startsWith("http")
        ? img
        : `${BACKEND_URL}${img}`
      : DEFAULT_IMAGE;

  // FETCH PRODUCTS -----------------------------------------------------
// FETCH PRODUCTS -----------------------------------------------------
const fetchProductsHandler = async (pageNumber = 1) => {
  if (!token) return;

  try {
    const data = await getProducts(token, {
      page: pageNumber,
      pageSize: 20, // optional (backend default is already 20)
    });

    const formatted = data.results.map((p: Product) => ({
      ...p,
      image: normalizeImage(p.image),
    }));

    if (pageNumber === 1) {
      // First page  carousel + full list reset
      setProducts(formatted);       // carousel (first 20)
      setAllProducts(formatted);    // full-page list
    } else {
      // Next pages  append
      setAllProducts((prev) => [...prev, ...formatted]);
    }

    setHasMore(Boolean(data.next)); // backend tells if more pages exist
    setPage(pageNumber);
  } catch (err) {
    console.error("Product fetch error:", err);
  }
};


  // SELLER SEARCH (simple: filters local product list)
  const handleSearch = (query: string) => {
    if (!query.trim()) {
      setSearching(false);
      setSearchResults([]);
      setActivePanel("products");
      return;
    }

    const filtered = products.filter((p) =>
      p.name.toLowerCase().includes(query.toLowerCase()) ||
      (p.ref_no && p.ref_no.toLowerCase().includes(query.toLowerCase()))
    );

    setSearchResults(filtered);
    setSearching(true);
    setActivePanel("products");
  };

  // FETCH NOTIFS -----------------------------------------------------
  const fetchNotificationsHandler = async () => {
    if (!token) return;

    try {
      const data = await getNotifications(token);
      setNotifications(data);
    } catch (err) {
      console.error("Notifications error:", err);
    }
  };

  // FETCH QUOTATIONS -----------------------------------------------------
  const fetchQuotationRequestsHandler = async () => {
    if (!token) return;

    try {
      const data = await getQuotationRequests(token);
      setQuotationRequests(data);
    } catch (err) {
      console.error("Quotation request error:", err);
    }
  };

  // TOKEN -----------------------------------------------------

  useEffect(() => {
    const stored = localStorage.getItem("access_token");
    if (!stored) {
      navigate("/login");
      return;
    }
    setToken(stored);
    setLoading(false);
    if (token) {
      fetchProductsHandler(1);
      fetchNotificationsHandler();
    }
  }, [token]);

  const unreadCount = notifications.filter((n) => !n.is_read).length;

  // PANEL CONTENT -----------------------------------------------------
  let bodyContent: JSX.Element | null = null;

  if (selectedProduct) {
    bodyContent = (
      <UpdateProduct
        token={token!}
        product={selectedProduct}
        onBack={() => setSelectedProduct(null)}
      />
    );
  }

  else if (searching) {
    //  SEARCH RESULTS PAGE (matches BuyerHome style)
    bodyContent = (
      <div className="relative w-full max-w-[1440px] mx-auto">

        {/* CLOSE BUTTON */}
        <button
          onClick={() => {
            setSearching(false);
            setSearchResults([]);
            setActivePanel("products");
          }}
          className="absolute top-3 right-3 z-20 text-gray-700 hover:text-black"
        >
          
        </button>

        {searchResults.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-20">
            <h2 className="text-lg font-semibold text-gray-600 mt-4">
              No products found
            </h2>
            <p className="text-sm text-gray-500">
              Try a different search.
            </p>
          </div>
        ) : (
          <ProductsList
            title="Search Results"
            products={searchResults}
            onSelectProduct={(product) => {
              setSelectedProduct(product);
            }}
          />
        )}
      </div>
    );
  }

  else {
    switch (activePanel) {
      case "dashboard":
        bodyContent = (
          <SellerDashboard
            token={token!}
            onClose={() => {
              setActivePanel("products");
              fetchProductsHandler();
            }}
          />
        );
        break;

      case "profile":
        bodyContent = <SellerProfile token={token!} onClose={() => setActivePanel("products")} />;
        break;

      case "about":
        bodyContent = <About />;
        break;

      case "notifications":
        bodyContent = (
          <NotificationsPanel
            notifications={notifications}
            title="Your Notifications"
            onClose={() => setActivePanel("products")}
            onMarkRead={async (id) => {
              await markNotificationRead(token!, id);
              const updated = await getNotifications(token!);
              setNotifications(updated);
            }}
          />
        );
        break;

      case "chat":
        bodyContent = <Chat token={token!} userEmail="seller@example.com" onClose={() => setActivePanel("products")} />;
        break;

      case "orders":
        bodyContent = <OrderPanel token={token!} onClose={() => setActivePanel("products")} />;
        break;

      case "quotations":
        bodyContent = (
          <QuotationPanel
            token={token!}
            requests={quotationRequests}
            onClose={() => setActivePanel("products")}
          />
        );
        break;

      case "addProduct":
        bodyContent = (
          <AddProduct
            token={token!}
            onClose={() => {
              setActivePanel("products");
              fetchProductsHandler();
            }}
          />
        );
        break;

      case "uploadProduct":
        bodyContent = (
          <UploadProduct
            token={token!}
            onSuccess={() => {
              fetchProductsHandler();
              setActivePanel("products");
            }}
            onClose={() => setActivePanel("products")}
          />
        );
        break;
      case "products_all":
        bodyContent = (
          <DisplayProducts
            products={allProducts}
            hasMore={hasMore}
            onLoadMore={() => fetchProductsHandler(page + 1)}
            onSelectProduct={(product) => setSelectedProduct(product)}
            onClose={() => setActivePanel("products")}   //  CLOSE BUTTON WORKS

          />
        );
        break;


      case "products":
      default:
        bodyContent = (
          <div className="w-full max-w-[1440px] mx-auto flex flex-col gap-8 pb-8">
            <div className="
              w-full rounded-xl overflow-hidden shadow
              bg-gray-100 flex items-center justify-center
              h-[420px] xl:h-[420px] lg:h-[360px] md:h-[300px] sm:h-[240px] h-[200px]
            ">
              <BannerAd />
            </div>

            {/* SECTION HEADER */}
            <div className="flex justify-between items-center">
              <h2 className="text-2xl font-semibold">Your Products</h2>

              <div className="flex gap-3">
                <button
                  onClick={() => setActivePanel("uploadProduct")}
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                >
                  Upload
                </button>
                <button
                  onClick={() => setActivePanel("addProduct")}
                  className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
                >
                  Add
                </button>
              </div>
            </div>

            {/* PRODUCTS */}
            <ProductsList
              title="Your Products"
              products={products}
              onSelectProduct={(product) => setSelectedProduct(product)}
              onSeeAll={() => {
                setActivePanel("products_all");
                fetchProductsHandler(1);
              }}
            />

          </div>
        );
        break;
    }
  }

  // MAIN RETURN -----------------------------------------------------
  return (
    <div className="flex flex-col h-screen bg-gray-100">
      <Header
        token={token}
        onUserPress={() => setActivePanel("profile")}
        onDashboardPress={() => setActivePanel("dashboard")}
        onSearch={handleSearch}
        unreadCount={unreadCount}
        onOrdersPress={() => setActivePanel("orders")}
        onChatPress={() => setActivePanel("chat")}
        onNotificationsPress={() => setActivePanel("notifications")}
        onQuotationsPress={() => {
          fetchQuotationRequestsHandler();
          setActivePanel("quotations");
        }}
        onAboutPress={() => setActivePanel("about")}
        onLogout={() => {
          localStorage.clear();
          navigate("/login");
        }}
      />

      <div className="flex-1 overflow-y-auto px-4 md:px-8 py-5">
        {bodyContent}
      </div>
    </div>
  );
}
