import React, { useState, useEffect } from "react";
import {
  addToCart,
  getProductReviews,
  checkReviewEligibility,
  sendQuotationRequest,
  addRecentView,
} from "../../api/api";

import { Product, ProductVariant, Review } from "../../types";
import ProductGallery from "./ProductGallery";

type Props = {
  product: Product;
  token?: string;
  onBack: () => void;
  onAddToCart: (
    product: Product,
    quantity: number,
    variant?: ProductVariant
  ) => void;
};

export default function ProductDetails({
  product,
  token,
  onBack,
  onAddToCart,
}: Props) {
  const [selectedVariant, setSelectedVariant] =
    useState<ProductVariant | null>(
      product.variants?.[0] || null
    );

  const [quantity, setQuantity] = useState(1);
  const [reviews, setReviews] = useState<Review[]>([]);

  const [loading, setLoading] = useState(false);
  const [quotationLoading, setQuotationLoading] = useState(false);

  /* ---------------- Images ---------------- */
  const productImages = product.image ? [product.image] : [];

  /* ---------------- Safe number parser ---------------- */
  const toNum = (v: unknown): number | null => {
    const n = Number(v);
    return isNaN(n) ? null : n;
  };

  /* =====================================================
     VARIANT-BASED PRICING (FIXED)
  ====================================================== */

  const rawBasePrice =
    selectedVariant?.price ??
    selectedVariant?.est_price ??
    null;

  const basePrice = toNum(rawBasePrice);
  const discount = toNum(selectedVariant?.discount) || 0;
  const gst = toNum(product.gst) || 0;

  const discountedPrice =
    basePrice !== null
      ? basePrice - basePrice * (discount / 100)
      : null;

  const gstPrice =
    discountedPrice !== null
      ? discountedPrice + discountedPrice * (gst / 100)
      : null;

  /* ---------------- Load reviews ---------------- */
  useEffect(() => {
    (async () => {
      try {
        const data = await getProductReviews(product.id.toString());
        setReviews(data || []);
      } catch {}

      if (token) {
        addRecentView(token, product.id);
        try {
          await checkReviewEligibility(token, product.id.toString());
        } catch {}
      }
    })();
  }, [product.id, token]);

  /* ---------------- Quantity ---------------- */
  const increment = () => setQuantity((q) => q + 1);
  const decrement = () => setQuantity((q) => Math.max(1, q - 1));

  /* ---------------- Add to cart ---------------- */
  const handleAddToCart = async () => {
    if (!token) return alert("Please log in");

    try {
      setLoading(true);

      await addToCart(
        token,
        product.id,
        quantity,
        selectedVariant ? selectedVariant.id : null
      );

      onAddToCart(product, quantity, selectedVariant || undefined);
      alert("Added to cart!");
    } catch {
      alert("Failed to add to cart");
    } finally {
      setLoading(false);
    }
  };

  /* ---------------- Send quotation ---------------- */
  const handleSendQuotation = async () => {
    if (!token) return alert("Please log in");
    if (!quantity || quantity < 1) return alert("Enter valid quantity");

    try {
      setQuotationLoading(true);

      await sendQuotationRequest(
        token,
        product.id,
        selectedVariant ? selectedVariant.id : null,
        quantity
      );

      alert(`Quotation requested for quantity: ${quantity}`);
    } catch {
      alert("Failed to send quotation");
    } finally {
      setQuotationLoading(false);
    }
  };

  /* =====================================================
     UI
  ====================================================== */

  return (
    <div className="p-4 max-w-6xl mx-auto">
      {/* BACK */}
      <button
        onClick={onBack}
        className="text-blue-600 hover:underline mb-4"
      >
         Back to Products
      </button>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {/* GALLERY */}
        <ProductGallery images={productImages} />

        {/* INFO */}
        <div>
          <h1 className="text-3xl font-bold mb-1">{product.name}</h1>

          <div className="text-gray-700 space-y-1 mb-4">
            <p><b>Ref No:</b> {product.ref_no || ""}</p>
            <p><b>Brand:</b> {product.brand || ""}</p>
            <p><b>Category:</b> {product.category || ""}</p>
            <p><b>HSN:</b> {product.hsn || ""}</p>
            <p><b>CAS No:</b> {product.cas_no || ""}</p>
          </div>

          <p className="text-gray-600 mb-4">
            Seller: <b>{product.seller}</b>
          </p>

          {/* PRICE */}
          <div className="mb-4">
            {discountedPrice !== null ? (
              <div className="space-y-1">
                <p className="text-green-600 text-2xl font-semibold">
                  {discountedPrice.toFixed(2)}
                </p>

                {discount > 0 && basePrice !== null && (
                  <>
                    <p className="text-gray-500 line-through text-sm">
                      MRP: {basePrice.toFixed(2)}
                    </p>
                    <p className="text-red-600 text-sm">
                      You save {(basePrice - discountedPrice).toFixed(2)} ({discount}%)
                    </p>
                  </>
                )}

                {gst > 0 && (
                  <p className="text-blue-600 text-sm">
                    Price incl. GST ({gst}%): {gstPrice?.toFixed(2)}
                  </p>
                )}
              </div>
            ) : (
              <p className="text-red-500">Price unavailable</p>
            )}
          </div>

          {/* VARIANTS */}
          {product.variants?.length ? (
            <div className="mb-4">
              <p className="font-medium mb-1">Choose Variant:</p>
              <div className="flex gap-2 flex-wrap">
                {product.variants.map((v) => (
                  <button
                    key={v.id}
                    onClick={() => setSelectedVariant(v)}
                    className={`px-3 py-1 rounded border ${
                      selectedVariant?.id === v.id
                        ? "bg-blue-600 text-white"
                        : "bg-gray-100 hover:bg-gray-200"
                    }`}
                  >
                    {v.variant_label}
                  </button>
                ))}
              </div>
            </div>
          ) : null}

          {/* QUANTITY */}
          <div className="mb-6">
            <label className="font-medium">Quantity</label>
            <div className="mt-1 flex items-center gap-2">
              <button
                onClick={decrement}
                className="px-3 py-1 bg-gray-200 rounded"
              >
                
              </button>

              <input
                type="number"
                min={1}
                value={quantity}
                onChange={(e) => setQuantity(Number(e.target.value))}
                className="border px-3 py-1 w-28 rounded"
              />

              <button
                onClick={increment}
                className="px-3 py-1 bg-gray-200 rounded"
              >
                +
              </button>
            </div>
          </div>

          {/* ACTIONS */}
          <div className="flex flex-col gap-2">
            <button
              onClick={handleAddToCart}
              disabled={loading}
              className="bg-green-600 text-white py-2 rounded hover:bg-green-700"
            >
              {loading ? "Adding..." : "Add to Cart"}
            </button>

            <button
              onClick={handleSendQuotation}
              disabled={quotationLoading}
              className="bg-blue-600 text-white py-2 rounded hover:bg-blue-700"
            >
              {quotationLoading ? "Sending..." : "Request Quotation"}
            </button>
          </div>
        </div>
      </div>

      {/* DESCRIPTION */}
      <div className="mt-10">
        <h2 className="text-xl font-semibold mb-2">Item Details</h2>
        <p className="text-gray-700">
          {product.description || "No description available."}
        </p>
      </div>

      {/* REVIEWS */}
      <div className="mt-10">
        <h2 className="text-xl font-semibold mb-4">Customer Reviews</h2>

        {reviews.length === 0 ? (
          <p>No reviews yet</p>
        ) : (
          reviews.map((r) => (
            <div
              key={r.id}
              className="border rounded p-3 mb-3 shadow-sm bg-white"
            >
              <div className="flex justify-between mb-1">
                <span className="text-yellow-500 text-lg">
                  {"".repeat(r.rating)}
                </span>
                <span className="text-gray-500 text-sm">
                  {r.buyer}
                </span>
              </div>
              <p>{r.comment}</p>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
