import React from "react";
import { Heart, X, ShoppingCart } from "lucide-react";
import { Product, ProductVariant } from "../../types";

type Props = {
  products: Product[];
  onClose: () => void;
  onToggleWishlist: (product: Product) => void;
  onSelectProduct?: (product: Product) => void;
  onAddToCart: (
    product: Product,
    quantity: number,
    variant?: ProductVariant
  ) => void;
};

export default function WishlistPanel({
  products,
  onClose,
  onToggleWishlist,
  onSelectProduct,
  onAddToCart,
}: Props) {
  const DEFAULT_IMAGE = "https://via.placeholder.com/300";

  /* ---------------- Variant-safe mapping ---------------- */
  const safeProducts = products.map((p) => {
    const variant = p.variants?.[0] ?? null;

    const basePrice =
      variant?.price ??
      variant?.est_price ??
      null;

    const price =
      basePrice !== null ? Number(basePrice) : null;

    const discount = Number(variant?.discount || 0);

    const discountedPrice =
      price !== null && discount > 0
        ? price - (price * discount) / 100
        : price;

    return {
      ...p,
      basePrice: price,
      discount,
      discountedPrice,
      image: p.image ?? DEFAULT_IMAGE,
      variant,
    };
  });

  return (
    <div className="w-full max-w-6xl mx-auto px-4 py-6">
      {/* HEADER */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h2 className="text-3xl font-bold">My Wishlist</h2>
          <p className="text-gray-600 text-sm">Your saved products</p>
        </div>

        <button
          onClick={onClose}
          className="p-2 rounded-full hover:bg-gray-200"
        >
          <X size={26} />
        </button>
      </div>

      {/* SUMMARY */}
      <div className="bg-blue-50 border border-blue-200 p-4 rounded-lg grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
        <div className="text-center">
          <p className="text-gray-600 text-sm">Total Items</p>
          <p className="text-2xl font-bold">{safeProducts.length}</p>
        </div>

        <div className="text-center">
          <p className="text-gray-600 text-sm">Total Value</p>
          <p className="text-2xl font-bold">
            
            {safeProducts
              .reduce(
                (sum, item) => sum + (item.discountedPrice || 0),
                0
              )
              .toFixed(2)}
          </p>
        </div>
      </div>

      {/* EMPTY */}
      {safeProducts.length === 0 && (
        <div className="text-center py-24">
          <p className="text-6xl mb-2"></p>
          <h3 className="text-xl font-semibold mt-2">
            Your wishlist is empty
          </h3>
          <p className="text-gray-600 text-sm">
            Start adding items to save them here.
          </p>
        </div>
      )}

      {/* GRID */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-7">
        {safeProducts.map((item) => (
          <div
            key={item.id}
            className="border rounded-lg shadow-sm hover:shadow-md bg-white overflow-hidden"
          >
            {/* IMAGE */}
            <div className="relative">
              <img
                src={item.image}
                className="w-full h-48 object-cover"
              />

              {/* Remove */}
              <button
                onClick={() => onToggleWishlist(item)}
                className="absolute top-3 right-3 bg-white text-red-600 p-2 rounded-full shadow"
              >
                <Heart size={18} />
              </button>

              {/* Discount badge */}
              {item.discount > 0 && (
                <div className="absolute top-3 left-3 bg-red-600 text-white px-2 py-1 text-xs rounded">
                  -{item.discount}%
                </div>
              )}
            </div>

            {/* CONTENT */}
            <div className="p-4">
              <h3 className="font-semibold text-lg">{item.name}</h3>
              <p className="text-sm text-gray-600">{item.category}</p>

              {/* PRICE */}
              {item.basePrice !== null ? (
                <div className="mt-3">
                  {item.discount > 0 ? (
                    <>
                      <span className="text-red-600 font-bold text-xl">
                        {item.discountedPrice?.toFixed(2)}
                      </span>
                      <span className="ml-2 line-through text-gray-500">
                        {item.basePrice.toFixed(2)}
                      </span>
                    </>
                  ) : (
                    <span className="font-semibold text-xl">
                      {item.basePrice.toFixed(2)}
                    </span>
                  )}
                </div>
              ) : (
                <p className="text-red-500 text-sm">Out of stock</p>
              )}

              {/* VIEW */}
              <button
                onClick={() => onSelectProduct?.(item)}
                className="text-gray-800 mt-3 text-sm hover:underline"
              >
                View Product
              </button>

              {/* ADD TO CART */}
              <button
                onClick={() =>
                  onAddToCart(item, 1, item.variant ?? undefined)
                }
                className="w-full bg-blue-600 hover:bg-blue-700 text-white py-2 rounded-lg mt-4 flex items-center justify-center gap-2"
                disabled={!item.basePrice}
              >
                <ShoppingCart size={18} />
                Add to Cart
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
