import React, { useEffect, useState, useRef } from "react";
import { X } from "lucide-react";
import { getConversations, getMessages, sendMessage } from "../../api/api";

type ChatPanelProps = {
  token: string;
  userEmail: string;
  onClose?: () => void; // <-- ADD THIS for closing the chat panel
};

type Conversation = {
  id: number;
  buyer: string;
  seller: string;
  product: { name: string } | null;
  last_message?: string;
  unread_count?: number;
};

type Message = {
  id: number;
  sender: string;
  text: string;
  created_at: string;
};

export default function ChatPanel({ token, userEmail, onClose }: ChatPanelProps) {
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [messages, setMessages] = useState<Message[]>([]);
  const [selected, setSelected] = useState<number | null>(null);
  const [newMessage, setNewMessage] = useState("");
  const [loading, setLoading] = useState(true);

  const [mobileView, setMobileView] = useState<"list" | "chat">("list");

  const messagesEndRef = useRef<HTMLDivElement>(null);

  const isMobile = () => window.innerWidth < 1024;

  useEffect(() => {
    const load = async () => {
      try {
        const data = await getConversations(token);
        setConversations(data);
      } catch {}
      setLoading(false);
    };
    load();

    const listener = () => {
      if (!isMobile()) setMobileView("list");
    };
    window.addEventListener("resize", listener);
    return () => window.removeEventListener("resize", listener);
  }, []);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const loadMessages = async (id: number) => {
    setSelected(id);
    if (isMobile()) setMobileView("chat");

    try {
      const data = await getMessages(token, id);
      setMessages(data);
    } catch {}
  };

  const send = async () => {
    if (!newMessage.trim() || !selected) return;
    try {
      const msg = await sendMessage(token, selected, newMessage);
      setMessages((prev) => [...prev, msg]);
      setNewMessage("");

      setConversations((prev) =>
        prev.map((c) =>
          c.id === selected ? { ...c, last_message: msg.text } : c
        )
      );
    } catch {}
  };

  const getInitials = (name: string = "U") =>
    name
      .split(" ")
      .map((n) => n[0])
      .join("")
      .toUpperCase();

  // ---------------------
  // MAIN LAYOUT
  // ---------------------
  return (
    <div className="w-full h-screen bg-white flex overflow-hidden">

      {/* LEFT LIST */}
      <div
        className={`
          lg:w-1/3 w-full h-full flex-col border-r border-gray-200 bg-gray-50
          ${isMobile() && mobileView === "chat" ? "hidden" : "flex"}
        `}
      >
        <div className="p-4 border-b bg-white flex justify-between items-center">
          <h2 className="font-semibold text-gray-800 text-lg">Messages</h2>

          {/* CLOSE BUTTON for entire chat panel (optional) */}
          {onClose && (
            <button
              onClick={onClose}
              className="p-2 rounded-full hover:bg-gray-200 transition"
            >
              <X size={22} className="text-gray-700" />
            </button>
          )}
        </div>

        <div className="flex-1 overflow-y-auto">
          {loading ? (
            <p className="p-4 text-center text-gray-500">Loading...</p>
          ) : conversations.length === 0 ? (
            <p className="p-4 text-center text-gray-500">No messages yet</p>
          ) : (
            conversations.map((conv) => {
              const partner =
                conv.seller === userEmail ? conv.buyer : conv.seller;

              return (
                <div
                  key={conv.id}
                  className={`flex items-center gap-4 px-4 py-3 cursor-pointer hover:bg-gray-100 ${
                    selected === conv.id ? "bg-blue-50" : ""
                  }`}
                  onClick={() => loadMessages(conv.id)}
                >
                  <div className="w-10 h-10 rounded-full bg-blue-600 text-white flex items-center justify-center">
                    {getInitials(partner)}
                  </div>

                  <div className="flex-1">
                    <div className="flex justify-between">
                      <p className="font-medium text-gray-800">{partner}</p>
                      {conv.unread_count ? (
                        <span className="bg-red-500 text-white text-xs px-2 rounded-full">
                          {conv.unread_count}
                        </span>
                      ) : null}
                    </div>
                    <p className="text-xs text-gray-500 truncate">
                      {conv.last_message}
                    </p>
                  </div>
                </div>
              );
            })
          )}
        </div>
      </div>

      {/* RIGHT CHAT WINDOW */}
      <div
        className={`
          flex-1 h-full flex flex-col bg-white
          ${isMobile() && mobileView === "list" ? "hidden" : "flex"}
        `}
      >
        {!selected ? (
          <div className="flex flex-col items-center justify-center h-full text-center">
            <p className="text-gray-400 text-lg">Select a conversation</p>
          </div>
        ) : (
          <>
            {/* CHAT HEADER */}
            {(() => {
              const c = conversations.find((x) => x.id === selected);
              if (!c) return null;

              const partner =
                c.seller === userEmail ? c.buyer : c.seller;

              return (
                <div className="p-4 border-b flex items-center justify-between bg-white shadow-sm">
                  <div className="flex items-center gap-3">
                    {/* Mobile back */}
                    {isMobile() && (
                      <button
                        className="text-xl mr-2"
                        onClick={() => setMobileView("list")}
                      >
                        
                      </button>
                    )}

                    {/* Avatar */}
                    <div className="w-12 h-12 rounded-full bg-blue-600 text-white flex items-center justify-center">
                      {getInitials(partner)}
                    </div>

                    <div>
                      <p className="font-semibold text-gray-800">{partner}</p>
                      <p className="text-xs text-gray-500">
                        {c.product?.name ?? "Product"}
                      </p>
                    </div>
                  </div>

                  {/* CLOSE BUTTON (top-right) */}
                  {onClose && (
                    <button
                      onClick={onClose}
                      className="p-2 rounded-full hover:bg-gray-200 transition"
                    >
                      <X size={22} className="text-gray-700" />
                    </button>
                  )}
                </div>
              );
            })()}

            {/* MESSAGES */}
            <div className="flex-1 overflow-y-auto px-4 py-4">
              {messages.map((msg) => (
                <div
                  key={msg.id}
                  className={`flex mb-3 ${
                    msg.sender === userEmail ? "justify-end" : "justify-start"
                  }`}
                >
                  <div
                    className={`max-w-xs rounded-2xl px-3 py-2 text-sm shadow ${
                      msg.sender === userEmail
                        ? "bg-blue-600 text-white"
                        : "bg-gray-100 text-gray-800"
                    }`}
                  >
                    {msg.text}
                  </div>
                </div>
              ))}

              <div ref={messagesEndRef}></div>
            </div>

            {/* INPUT */}
            <div className="border-t p-3 flex gap-3 bg-white">
              <input
                className="flex-1 border rounded-full px-4 py-2 text-sm outline-none focus:ring-2 focus:ring-blue-300"
                placeholder="Type a message..."
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
                onKeyPress={(e) => e.key === "Enter" && send()}
              />
              <button
                className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-full"
                onClick={send}
              >
                Send
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}


