import React, { useEffect, useState } from "react";
import { getProfile, updateProfile } from "../../api/api";

const STATES = [
  "Andhra Pradesh",
  "Delhi",
  "Gujarat",
  "Karnataka",
  "Maharashtra",
  "Tamil Nadu",
  "Uttar Pradesh",
  "West Bengal",
];

const BUYER_CATEGORIES = [
  "University",
  "Research Lab",
  "Corporate R&D",
  "Medical / Pharma",
  "Student",
  "Government Org",
  "Other",
];

// ------------------------------
// TYPES
// ------------------------------
type ShippingAddress = {
  id?: number;
  name: string;
  phone: string;
  addressLine1: string;
  addressLine2?: string;
  city: string;
  state: string;
  pincode: string;
  isDefault: boolean;
};

type BillingAddress = {
  name: string;
  phone: string;
  addressLine1: string;
  addressLine2?: string;
  city: string;
  state: string;
  pincode: string;
};

type Props = {
  token: string;
  onClose: () => void;
};

// Convenience empty objects
const EMPTY_BILLING: BillingAddress = {
  name: "",
  phone: "",
  addressLine1: "",
  addressLine2: "",
  city: "",
  state: "",
  pincode: "",
};

const EMPTY_SHIPPING: ShippingAddress = {
  name: "",
  phone: "",
  addressLine1: "",
  addressLine2: "",
  city: "",
  state: "",
  pincode: "",
  isDefault: false,
};

// ------------------------------
// SMALL UI HELPERS
// ------------------------------
function SectionTitle({ children }: { children: React.ReactNode }) {
  return (
    <h3 className="mt-8 mb-3 text-lg font-semibold text-gray-900">
      {children}
    </h3>
  );
}

function Input({
  label,
  value,
  disabled,
  onChange,
}: {
  label: string;
  value: string;
  disabled: boolean;
  onChange: (v: string) => void;
}) {
  return (
    <div>
      <label className="block text-sm mb-1 font-medium text-gray-700">
        {label}
      </label>
      <input
        className={`w-full border px-3 py-2 rounded-lg text-sm ${
          disabled
            ? "bg-gray-100 border-gray-200 text-gray-500"
            : "bg-white border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
        }`}
        disabled={disabled}
        value={value}
        onChange={(e) => onChange(e.target.value)}
      />
    </div>
  );
}

function Select({
  label,
  value,
  disabled,
  options,
  onChange,
}: {
  label: string;
  value: string;
  disabled: boolean;
  options: string[];
  onChange: (v: string) => void;
}) {
  return (
    <div>
      <label className="block text-sm mb-1 font-medium text-gray-700">
        {label}
      </label>
      <select
        className={`w-full border px-3 py-2 rounded-lg text-sm ${
          disabled
            ? "bg-gray-100 border-gray-200 text-gray-500"
            : "bg-white border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
        }`}
        disabled={disabled}
        value={value}
        onChange={(e) => onChange(e.target.value)}
      >
        <option value="">Select</option>
        {options.map((o) => (
          <option key={o}>{o}</option>
        ))}
      </select>
    </div>
  );
}

// ------------------------------
// ADDRESS FORM (used for both)
// ------------------------------
function AddressForm({
  form,
  editMode,
  onChange,
  showDefault = false,
}: {
  form: ShippingAddress | BillingAddress;
  editMode: boolean;
  onChange: (field: string, value: string | boolean) => void;
  showDefault?: boolean;
}) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <Input
        label="Name"
        value={form.name}
        disabled={!editMode}
        onChange={(v) => onChange("name", v)}
      />

      <Input
        label="Phone"
        value={form.phone}
        disabled={!editMode}
        onChange={(v) => onChange("phone", v)}
      />

      <Input
        label="Address Line 1"
        value={form.addressLine1}
        disabled={!editMode}
        onChange={(v) => onChange("addressLine1", v)}
      />

      <Input
        label="Address Line 2"
        value={form.addressLine2 ?? ""}
        disabled={!editMode}
        onChange={(v) => onChange("addressLine2", v)}
      />

      <Input
        label="City"
        value={form.city}
        disabled={!editMode}
        onChange={(v) => onChange("city", v)}
      />

      <Select
        label="State"
        value={form.state}
        disabled={!editMode}
        options={STATES}
        onChange={(v) => onChange("state", v)}
      />

      <Input
        label="Pincode"
        value={form.pincode}
        disabled={!editMode}
        onChange={(v) => onChange("pincode", v)}
      />

      {showDefault && "isDefault" in form && (
        <div className="col-span-2 flex items-center mt-1">
          <input
            type="checkbox"
            disabled={!editMode}
            checked={(form as ShippingAddress).isDefault}
            onChange={(e) => onChange("isDefault", e.target.checked)}
            className="mr-2"
          />
          <span className="text-sm text-gray-700">
            Set as default shipping address
          </span>
        </div>
      )}
    </div>
  );
}

// ------------------------------
// MAIN COMPONENT
// ------------------------------
export default function UserProfile({ token, onClose }: Props) {
  const [loading, setLoading] = useState(true);
  const [editMode, setEditMode] = useState(false);
  const [saving, setSaving] = useState(false);

  // BASIC USER INFO
  const [form, setForm] = useState({
    name: "",
    email: "",
    phone: "",
    username: "",
    buyerCategory: "",
    organizationName: "",
  });

  // SHIPPING & BILLING
  const [shippingAddresses, setShippingAddresses] = useState<ShippingAddress[]>(
    []
  );
  const [billingForm, setBillingForm] =
    useState<BillingAddress>(EMPTY_BILLING);

  const [shippingForm, setShippingForm] =
    useState<ShippingAddress>(EMPTY_SHIPPING);

  const [isAddingShipping, setIsAddingShipping] = useState(false);
  const [editingAddressId, setEditingAddressId] = useState<number | null>(null);

  // ------------------------------
  // LOAD PROFILE
  // ------------------------------
  useEffect(() => {
    async function loadData() {
      try {
        const data = await getProfile(token);

        setForm({
          name: data.name || "",
          email: data.email || "",
          phone: data.phone || "",
          username: data.username || "",
          buyerCategory: data.buyerCategory || "",
          organizationName: data.organizationName || "",
        });

        setShippingAddresses(data.shippingAddresses || []);
        setBillingForm(data.billingAddress || { ...EMPTY_BILLING });
      } catch (err) {
        alert("Failed to load profile");
      } finally {
        setLoading(false);
      }
    }
    loadData();
  }, [token]);

  // ------------------------------
  // HANDLERS
  // ------------------------------
  const handleFormChange = (field: string, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleShippingChange = (field: string, value: string | boolean) => {
    setShippingForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleBillingChange = (field: string, value: string | boolean) => {
    if (typeof value === "boolean") return; // billing has no boolean fields
    setBillingForm((prev) => ({ ...prev, [field]: value }));
  };

  const resetShippingForm = () => {
    setShippingForm({
      ...EMPTY_SHIPPING,
      isDefault: shippingAddresses.length === 0,
    });
  };

  // ------------------------------
  // SAVE PROFILE
  // ------------------------------
  const handleSave = async () => {
    try {
      setSaving(true);

      const payload = {
        ...form,
        shippingAddresses,
        billingAddress: billingForm,
      };

      await updateProfile(token, payload);

      alert("Profile updated successfully!");
      setEditMode(false);
      setIsAddingShipping(false);
      setEditingAddressId(null);
    } catch (error: any) {
      alert(error.message || "Update failed");
    } finally {
      setSaving(false);
    }
  };

  // ------------------------------
  // RENDER
  // ------------------------------
  if (loading) {
    return (
      <div className="flex justify-center mt-10 text-gray-600">
        Loading profile
      </div>
    );
  }

  return (
    <div className="w-full max-w-3xl mx-auto bg-white shadow-sm border border-gray-200 rounded-xl p-6 mt-6">
      {/* HEADER */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <h2 className="text-xl font-semibold text-gray-900">My Profile</h2>
          <p className="text-xs text-gray-500 mt-1">
            Manage your personal details, shipping and billing addresses.
          </p>
        </div>
        <button
          onClick={onClose}
          className="px-3 py-1 text-sm rounded-md bg-gray-100 hover:bg-gray-200 text-gray-700"
        >
          Close
        </button>
      </div>

      {/* BASIC INFO */}
      <SectionTitle>Basic Information</SectionTitle>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <Input
          label="Full Name"
          disabled={!editMode}
          value={form.name}
          onChange={(v) => handleFormChange("name", v)}
        />
        <Input
          label="Email"
          disabled={!editMode}
          value={form.email}
          onChange={(v) => handleFormChange("email", v)}
        />
        <Input
          label="Mobile"
          disabled={!editMode}
          value={form.phone}
          onChange={(v) => handleFormChange("phone", v)}
        />
        <Input
          label="Username"
          disabled={!editMode}
          value={form.username}
          onChange={(v) => handleFormChange("username", v)}
        />
        <Select
          label="Buyer Category"
          disabled={!editMode}
          value={form.buyerCategory}
          options={BUYER_CATEGORIES}
          onChange={(v) => handleFormChange("buyerCategory", v)}
        />
        <Input
          label="Organization Name"
          disabled={!editMode}
          value={form.organizationName}
          onChange={(v) => handleFormChange("organizationName", v)}
        />
      </div>

      {/* SHIPPING ADDRESSES */}
      <SectionTitle>Shipping Addresses</SectionTitle>
      <div className="space-y-4">
        {shippingAddresses.length > 0 ? (
          shippingAddresses.map((addr) => (
            <div
              key={addr.id}
              className="border border-gray-200 rounded-lg bg-gray-50 px-4 py-3"
            >
              <div className="flex justify-between gap-4">
                <div>
                  <p className="font-medium text-sm text-gray-900">
                    {addr.name}{" "}
                    <span className="text-xs text-gray-500">
                      ({addr.phone})
                    </span>
                  </p>
                  <p className="text-xs text-gray-600 mt-1">
                    {addr.addressLine1}
                    {addr.addressLine2 ? `, ${addr.addressLine2}` : ""},{" "}
                    {addr.city}, {addr.state} - {addr.pincode}
                  </p>
                  {addr.isDefault && (
                    <span className="inline-flex mt-2 items-center px-2 py-0.5 rounded-full text-[11px] font-medium bg-green-50 text-green-700 border border-green-200">
                       Default Shipping
                    </span>
                  )}
                </div>

                {editMode && (
                  <div className="flex flex-col items-end gap-1 text-xs">
                    <button
                      className="text-blue-600 hover:underline"
                      onClick={() => {
                        setEditingAddressId(addr.id || null);
                        setIsAddingShipping(true);
                        setShippingForm(addr);
                      }}
                    >
                      Edit
                    </button>
                    <button
                      className="text-red-600 hover:underline"
                      onClick={() =>
                        setShippingAddresses((prev) =>
                          prev.filter((a) => a.id !== addr.id)
                        )
                      }
                    >
                      Delete
                    </button>
                    {!addr.isDefault && (
                      <button
                        className="text-gray-600 hover:underline mt-1"
                        onClick={() =>
                          setShippingAddresses((prev) =>
                            prev.map((a) => ({
                              ...a,
                              isDefault: a.id === addr.id,
                            }))
                          )
                        }
                      >
                        Make Default
                      </button>
                    )}
                  </div>
                )}
              </div>
            </div>
          ))
        ) : (
          <p className="text-gray-500 text-sm">
            No shipping addresses added yet.
          </p>
        )}

        {/* New / Edit Shipping Address Form Trigger */}
        {editMode && !isAddingShipping && (
          <button
            className="text-sm text-blue-600 hover:underline"
            onClick={() => {
              setEditingAddressId(null);
              resetShippingForm();
              setIsAddingShipping(true);
            }}
          >
            + Add New Shipping Address
          </button>
        )}

        {/* Shipping Address Form (Add / Edit) */}
        {editMode && isAddingShipping && (
          <div className="border border-gray-200 rounded-lg bg-white shadow-sm px-4 py-4">
            <div className="flex items-center justify-between mb-3">
              <h4 className="text-sm font-semibold text-gray-800">
                {editingAddressId ? "Edit Shipping Address" : "New Shipping Address"}
              </h4>
            </div>

            <AddressForm
              form={shippingForm}
              editMode={true}
              showDefault={true}
              onChange={handleShippingChange}
            />

            <div className="flex justify-end gap-3 mt-4">
              <button
                className="px-3 py-1.5 text-xs rounded-md bg-gray-100 hover:bg-gray-200 text-gray-700"
                onClick={() => {
                  setIsAddingShipping(false);
                  setEditingAddressId(null);
                  resetShippingForm();
                }}
              >
                Cancel
              </button>

              <button
                className="px-4 py-1.5 text-xs rounded-md bg-green-600 hover:bg-green-700 text-white font-medium"
                onClick={() => {
                  const cleaned: ShippingAddress = {
                    ...shippingForm,
                  };

                  // For new addresses ensure id is undefined so backend treats as create
                  if (!editingAddressId) {
                    delete (cleaned as any).id;
                    setShippingAddresses((prev) => [...prev, cleaned]);
                  } else {
                    setShippingAddresses((prev) =>
                      prev.map((a) =>
                        a.id === editingAddressId ? { ...cleaned, id: a.id } : a
                      )
                    );
                  }

                  setIsAddingShipping(false);
                  setEditingAddressId(null);
                  resetShippingForm();
                }}
              >
                Save Address
              </button>
            </div>
          </div>
        )}
      </div>

      {/* BILLING ADDRESS */}
      <SectionTitle>Billing Address</SectionTitle>
      <div className="mt-1 border border-gray-200 rounded-lg bg-gray-50 px-4 py-4">
        <AddressForm
          form={billingForm}
          editMode={editMode}
          onChange={handleBillingChange}
        />
      </div>

      {/* ACTION BUTTONS */}
      <div className="mt-6 flex justify-end gap-3">
        {!editMode ? (
          <button
            className="px-4 py-2 text-sm rounded-md bg-blue-600 hover:bg-blue-700 text-white font-medium"
            onClick={() => setEditMode(true)}
          >
            Edit Profile
          </button>
        ) : (
          <>
            <button
              className="px-4 py-2 text-sm rounded-md bg-gray-200 hover:bg-gray-300 text-gray-800"
              onClick={() => {
                setEditMode(false);
                setIsAddingShipping(false);
                setEditingAddressId(null);
              }}
            >
              Cancel
            </button>

            <button
              className="px-4 py-2 text-sm rounded-md bg-green-600 hover:bg-green-700 text-white font-medium"
              disabled={saving}
              onClick={handleSave}
            >
              {saving ? "Saving" : "Save Changes"}
            </button>
          </>
        )}
      </div>
    </div>
  );
}



